import HeaderTemplate from "../../../page_templates/header_template";
import SidePane from "../../../page_templates/side_pane";
import { useState, useEffect } from "react";
import WebAPIServices from "../../../../serv/apiservices";
const Payout = () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {
    Data();
  }, []);

  const Data = async () => {
    await WebAPIServices.Payout().then((jsonResponse) => {
      console.log(jsonResponse);
      if (jsonResponse.returnStatusCode === 200) {
        setData(jsonResponse.dynamicDomainModel);
      }
    });
  };
  return (
    <div
      className="position-absolute d-flex"
      style={{ width: "100%", height: "100%" }}
    >
      <SidePane />
      <div className="col-12 col-lg-10 overflow-auto">
        <HeaderTemplate />
        <div className="p-3">
          {data.length > 0 && (
            <>
              {data.map((item, key) => {
                return (
                  <>
                    <div key={key} className="mt-3">
                      <h3>Payout</h3>
                      <div className="p-2 pt-3 d-flex justify-content-between border-top border-bottom border-light">
                        <h6>Cashier</h6>
                        <p>{item.key}</p>
                      </div>
                      <div className="p-2 d-flex justify-content-between border-bottom border-light">
                        <h6>Total Sales</h6>
                        <p>{item.value}</p>
                      </div>
                    </div>
                  </>
                );
              })}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Payout;
