import HeaderTemplate from "../../page_templates/header_template";
import SidePane from "../../page_templates/side_pane";
import { useState, useEffect } from "react";
import WebAPIServices from "../../../serv/apiservices";
const Supplier = () => {
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {
    Data();
  }, [response]);

  const Data = async () => {
    await WebAPIServices.GetAllSuppliers()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setData(jsonResponse.dynamicDomainModel);
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <h4>Suppliers</h4>
            <div>
              <table className="table">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Supplier</th>
                    <th>Email</th>
                    <th>Address</th>
                    <th>Phone</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((item, key) => {
                    return (
                      <>
                        <tr key="key">
                          <td>{key + 1}</td>
                          <td>{item.supplierName}</td>
                          <td>{item.email}</td>
                          <td>{item.address}</td>
                          <td>{item.companyPhone1}</td>
                        </tr>
                      </>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Supplier;
