import SidePane from "../../../page_templates/side_pane";
import HeaderTemplate from "../../../page_templates/header_template";
import { useState } from "react";
import WebAPIServices from "../../../../serv/apiservices";
const AccountStatement = () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [accountno, setAccountNo] = useState("");

  const Data = async () => {
    await WebAPIServices.AccountStatement(startDate, endDate, accountno)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
          setLoading(true);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <label>Account no</label>
            <div className="col-3 mb-3 mbt-3">
              <input
                type="text"
                className="form-control"
                placeholder="enter account no"
                onKeyUp={(e) => setAccountNo(e.target.value)}
              />
            </div>
            <div className="d-flex justify-content-around col-8">
              <div>
                <div className="d-flex">
                  StartDate:
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setStartDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <div className="d-flex">
                  EndDate
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setEndDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <button
                  className="btn btn-sm btn-primary"
                  onClick={() => Data()}
                >
                  Generate report
                </button>
              </div>
            </div>
            <div className="p-3 border border-light mt-3">
              <h5>Account Information</h5>
              {data.map((acc) => {
                return (
                  <>
                    <div className="p-3">
                      <span className="d-flex justify-content-between border-bottom border-light">
                        <h6>Supplier:</h6>
                        <p>{acc.supplier.supplierName}</p>
                      </span>
                      <span className="d-flex justify-content-between border-bottom border-light pt-2 mb-2">
                        <h6>Sales person:</h6>
                        <p>{acc.salesPerson.fullName}</p>
                      </span>
                      <span className="border-bottom border-light">
                        <div>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Product name:</h6>
                            <p>{acc.productDomainModel.productName}</p>
                          </span>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Category name:</h6>
                            <p>{acc.productDomainModel.categoryName}</p>
                          </span>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Created by:</h6>
                            <p>{acc.productDomainModel.createdDateString}</p>
                          </span>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Price:</h6>
                            <p>{acc.productDomainModel.price}</p>
                          </span>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Product description:</h6>
                            <p>{acc.productDomainModel.productDescription}</p>
                          </span>
                          <span className="d-flex justify-content-between border-bottom border-light pt-2">
                            <h6>Variation:</h6>
                            <p>{acc.productDomainModel.variation}</p>
                          </span>
                        </div>
                      </span>
                      <span className="d-flex justify-content-between border-bottom border-light pt-2">
                        <h6>Purchased on:</h6>
                        <p>{acc.purchaseDateString}</p>
                      </span>
                    </div>
                  </>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default AccountStatement;
