import { useState, useEffect } from "react";
import WebAPIServices from "../../../../serv/apiservices";
import HeaderTemplate from "../../../page_templates/header_template";
import SidePane from "../../../page_templates/side_pane";
const BusinessLead = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    Data();
  }, []);

  const Data = async () => {
    await WebAPIServices.BusinessLead()
      .then((jsonResponse) => {
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <h3>Business Lead</h3>
            <table className="table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Cashier</th>
                  <th>Sales</th>
                </tr>
              </thead>
              <tbody>
                {data.length > 0 && (
                  <>
                    {data.map((data, key) => {
                      let prop = data.value;
                      let item = prop.split("|");
                      return (
                        <>
                          <tr key={key}>
                            <td>{key + 1}</td>
                            <td>{item[0]}</td>
                            <td>{item[1]}</td>
                          </tr>
                        </>
                      );
                    })}
                  </>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default BusinessLead;
