import HeaderTemplate from "../../page_templates/header_template";
import { useState, useEffect } from "react";
import {
  faBoxOpen,
  faCircleDot,
  faHand,
  faPencil,
  faSearch,
  faTrashAlt,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SidePane from "../../page_templates/side_pane";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { Form, NavItem } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import WebAPIServices from "../../../serv/apiservices";

const Profiles = () => {
  const [show, setShow] = useState(false);
  const [users, setUsers] = useState([]);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [role, setRole] = useState(0);
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState(false);
  const [user, setUser] = useState("");
  const [statuses, setStatuses] = useState([]);
  const [refresh, refreshState] = useState(false);
  const [edit, setEdit] = useState(false);
  const [_id, setID] = useState("");
  const [state, setState] = useState(1);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const CreateProfile = async () => {
    var profileDomainModel = {
      _id: "",
      FirstName: firstName,
      LastName: lastName,
      FullName: firstName + " " + lastName,
      Salt: "",
      Password: password,
      Email: email,
      Role: role,
      DateCreated: "2022-11-28T04:52:42.038Z",
      DateModified: "2022-11-28T04:52:42.038Z",
      Status: 1,
    };

    await WebAPIServices.CreateProfile(profileDomainModel)
      .then((jsonResponse) => {
        if (jsonResponse.returnStatusCode === 200) {
          setResponse(!response);
          console.log(jsonResponse);
        }
      })
      .catch((err) => err);
  };

  useEffect(() => {
    Data();
  }, [response]);

  const Data = async () => {
    await WebAPIServices.Profiles()
      .then((jsonResponse) => {
        if (jsonResponse.returnStatusCode === 200) {
          setLoading(true);
          setUsers(jsonResponse.dynamicDomainModel);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };

  const AddToState = (event) => {
    let _id = event.target.value;
    if (statuses.includes(_id)) {
      let filter = statuses.filter((e) => e !== _id);
      setStatuses(filter);
    } else {
      statuses.push(_id);
    }
  };

  const ActivateOne = async (id) => {
    await WebAPIServices.ProfileStatus(id, 1)
      .then((jsonResponse) => {
        refreshState(!refresh);
        setResponse(!response);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const ActivateMany = async () => {
    await WebAPIServices.ActivateProfiles(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        setResponse(!response);
        setStatuses([]);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const DisableOne = async (id) => {
    await WebAPIServices.ProfileStatus(id, 0)
      .then((jsonResponse) => {
        refreshState(!refresh);
        setResponse(!response);
        setStatuses([]);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeleteProfile = async (id) => {
    await WebAPIServices.DeleteProfile(id)
      .then((jsonResponse) => {
        setLoading(false);
        refreshState(!refresh);
        setResponse(!response);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeleteProfiles = async () => {
    await WebAPIServices.DeleteProfiles(statuses)
      .then((jsonResponse) => {
        setLoading(false);
        refreshState(!refresh);
        setResponse(!response);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeactivateMany = async () => {
    await WebAPIServices.DeactivateProfiles(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        setResponse(!response);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const handleEdit = async (id) => {
    await WebAPIServices.GetProfile(id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        const { _id, firstName, lastName, email, role } =
          jsonResponse.dynamicDomainModel;
        console.log(jsonResponse);
        setFirstName(firstName);
        setID(_id);
        setLastName(lastName);
        setEmail(email);
        setRole(role);
        setEdit(true);
      })
      .catch((err) => err);
  };

  const UpdateProfile = async () => {
    var profileDomainModel = {
      _id: _id,
      FirstName: firstName,
      LastName: lastName,
      FullName: firstName + " " + lastName,
      Salt: "",
      Password: password,
      Email: email,
      Role: role,
      DateCreated: "2022-11-28T04:52:42.038Z",
      DateModified: "2022-11-28T04:52:42.038Z",
      Status: 1,
    };
    await WebAPIServices.UpdateProfile(profileDomainModel)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setResponse(!response);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <div className="d-flex justify-content-between">
              <div className="d-flex">
                <button
                  className="btn btn-sm bg-dark text-white mt-2"
                  onClick={handleShow}
                >
                  {" "}
                  <FontAwesomeIcon icon={faBoxOpen} />
                  Create new
                </button>
                <button
                  className="btn btn-sm bg-warning text-dark mt-2"
                  onClick={() => DeactivateMany()}
                >
                  {" "}
                  <FontAwesomeIcon icon={faHand} />
                  Suspend
                </button>
                <button
                  className="btn btn-sm bg-success text-white mt-2"
                  onClick={() => ActivateMany()}
                >
                  {" "}
                  <FontAwesomeIcon icon={faCircleDot} />
                  Activate
                </button>
                <button
                  className="btn btn-sm bg-danger text-white mt-2"
                  onClick={() => DeleteProfiles()}
                >
                  {" "}
                  <FontAwesomeIcon icon={faTrashAlt} />
                  Delete
                </button>
                <small>
                  <Form.Select
                    style={{ width: "140px" }}
                    onChange={(e) => {
                      setState(parseInt(e.target.value));
                      setResponse(!response);
                    }}
                  >
                    <option value={1}>Active</option>
                    <option value={0}>Inactive</option>
                  </Form.Select>
                </small>
              </div>
              <div>
                <input
                  type="search"
                  className="form-control"
                  id="searchBar"
                  placeholder="Start searching products here....."
                  onKeyUp={async (e) => {
                    let bar = document.getElementById("searchBar").value;
                    if (bar === "") {
                      Data();
                    } else {
                      setUser(e.target.value);
                      await WebAPIServices.SearchUser(user)
                        .then((jsonResponse) => {
                          if (jsonResponse.returnStatusCode === 200) {
                            setLoading(true);
                            setUsers(jsonResponse.dynamicDomainModel);
                          } else {
                            setLoading(false);
                          }
                        })
                        .catch((err) => err);
                    }
                  }}
                />
              </div>
            </div>
          </div>
          <div className="p-2 overflow-auto">
            <span>Recent activities</span>
            <table className="table table-responsive text-dark">
              <thead>
                <tr className="text-dark">
                  <th scope="col">#</th>
                  <th scope="col">User</th>
                  <th scope="col">Role</th>
                  <th>Status</th>
                  <th scope="col">Edit</th>
                  <th scope="col">Delete</th>
                </tr>
              </thead>
              <tbody>
                {loading ? (
                  <>
                    {users.map((user, key) => {
                      if (user.status === state) {
                        return (
                          <>
                            <tr key={key}>
                              <th>
                                <input
                                  type="checkbox"
                                  value={user._id}
                                  onChange={(e) => AddToState(e)}
                                />
                              </th>
                              <td>{user.firstName + " " + user.lastName}</td>
                              <td>{user.role === 0 ? "Admin" : "Cashier"}</td>
                              <td>
                                {user.status === 0 ? (
                                  <>
                                    <NavItem>
                                      <FontAwesomeIcon
                                        icon={faCircleDot}
                                        onClick={() => ActivateOne(user._id)}
                                      />
                                      Activate
                                    </NavItem>
                                  </>
                                ) : (
                                  <>
                                    <NavItem
                                      onClick={() => DisableOne(user._id)}
                                    >
                                      <FontAwesomeIcon icon={faCircleDot} />
                                      Deactivate
                                    </NavItem>
                                  </>
                                )}
                              </td>
                              <td>
                                <NavItem
                                  onClick={async () => {
                                    handleEdit(user._id);
                                  }}
                                >
                                  <FontAwesomeIcon icon={faPencil} />
                                  Edit
                                </NavItem>
                              </td>
                              <td>
                                <NavItem
                                  onClick={() => DeleteProfile(user._id)}
                                >
                                  <FontAwesomeIcon icon={faTrashAlt} />
                                  Delete
                                </NavItem>
                              </td>
                            </tr>
                          </>
                        );
                      }
                    })}
                  </>
                ) : (
                  <>
                    <h3>Nothing to show</h3>
                  </>
                )}
              </tbody>
            </table>
          </div>
          <Modal show={show} onHide={handleClose}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Create User</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>FirstName</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter firstname"
                    onKeyUp={(e) => setFirstName(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>LastName</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter lastname"
                    onKeyUp={(e) => setLastName(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type="password"
                    placeholder="Enter password"
                    onKeyUp={(e) => setPassword(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Enter email address"
                    onKeyUp={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicRoles">
                  <Form.Label>Role</Form.Label>
                  <Form.Select
                    onChange={(e) => {
                      setRole(parseInt(e.target.value));
                    }}
                  >
                    <option value={0}>Manager</option>
                    <option value={1}>Cashier</option>
                    <option value={2}>Guest</option>
                  </Form.Select>
                </Form.Group>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="warning" onClick={handleClose}>
                  Close
                </Button>
                <Button
                  variant="dark"
                  onClick={() => {
                    CreateProfile();
                    handleClose();
                  }}
                >
                  Create User
                  <FontAwesomeIcon icon={faUser} />
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
          <Modal show={edit} onHide={() => setEdit(false)}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Update user</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>FirstName</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter firstname"
                    onKeyUp={(e) => setFirstName(e.target.value)}
                    defaultValue={firstName}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>LastName</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter lastname"
                    onKeyUp={(e) => setLastName(e.target.value)}
                    defaultValue={lastName}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Enter email address"
                    onKeyUp={(e) => setEmail(e.target.value)}
                    defaultValue={email}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicRoles">
                  <Form.Label>Role</Form.Label>
                  <Form.Select
                    onChange={(e) => {
                      setRole(parseInt(e.target.value));
                    }}
                  >
                    <option value={0}>Manager</option>
                    <option value={1}>Cashier</option>
                    <option value={2}>Third Party</option>
                  </Form.Select>
                </Form.Group>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="warning" onClick={handleClose}>
                  Close
                </Button>
                <Button
                  variant="dark"
                  onClick={() => {
                    UpdateProfile();
                    setEdit(false);
                  }}
                >
                  Update User
                  <FontAwesomeIcon icon={faUser} />
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
        </div>
      </div>
    </>
  );
};

export default Profiles;
