import HeaderTemplate from "../../page_templates/header_template";
import { useState } from "react";
import FormData from "form-data";
import {
  faAdd,
  faBoxOpen,
  faCancel,
  faCircle,
  faCircleDot,
  faLightbulb,
  faPencil,
  faTrashAlt,
  faTruckArrowRight,
  faTruckLoading,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SidePane from "../../page_templates/side_pane";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { Form } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import axios from "axios";
import WebAPIServices from "../../../serv/apiservices";
import { useEffect } from "react";
import EndpointNames from "../../../serv/endpoints";

const ReceivingStock = () => {
  const [show, setShow] = useState(false);
  const [edit, setEdit] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleShowEdit = () => setEdit(true);
  const handleCloseEdit = () => setEdit(false);
  const [loading, setLoading] = useState(false);
  const [refresh, refreshState] = useState(false);
  const [_id, setID] = useState("");
  const [productName, setProductName] = useState("");
  const [productPrice, setProductPrice] = useState("");
  const [productVariation, setProductVariation] = useState("");
  const [productDescription, setProductDescription] = useState("");
  const [serialNumber, setSerialNumber] = useState("");
  const [statuses, setStatuses] = useState([]);
  const [stateType, setStateType] = useState(0);
  const [state, setState] = useState(1);
  const [data, setData] = useState([]);
  const [productImage, setProductImage] = useState("");
  const [categoryId, setCategoryID] = useState("");
  const [categories, setCategories] = useState([]);
  const [suppliersName, setSuppliersName] = useState("");
  const [createdBy, setCreatedBy] = useState("");
  var imageString = "";
  let user = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    Data();
  }, [refresh]);

  const Data = async () => {
    await WebAPIServices.GetFullInvetory()
      .then((response) => {
        console.log(response);
        setData(response.dynamicDomainModel);
        if (response.returnStatusCode === 200) {
          setLoading(true);
        }
      })
      .catch((err) => err);
    await WebAPIServices.GetCategories()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setCategories(jsonResponse.dynamicDomainModel);
          setCategoryID(jsonResponse.dynamicDomainModel[0]._id);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    var productDomainModel = {
      _id: "aaa080056af5483fb47b8f1be70bfcf7",
      categoryName: "",
      categoryID: categoryId,
      productID: 1000,
      productName: productName,
      productDescription: productDescription,
      productSpecifications: "",
      price: productPrice,
      weight: "",
      imagePath: productImage,
      serialNumber: serialNumber,
      variation: productVariation,
      status: 1,
      productStatus: 0,
      createdDateString: "",
      dateModifiedString: "",
      productTimes: {
        dateCreated: "2022-11-28T04:52:42.038Z",
        dateModified: "2022-11-28T04:52:42.038Z",
        obsoluteDate: "2022-11-28T04:52:42.038Z",
        shipmentStartDate: "2022-11-28T04:52:42.038Z",
        shipmentProcesingDate: "2022-11-28T04:52:42.038Z",
        shipmentEndDate: "2022-11-28T04:52:42.038Z",
      },
    };
    await WebAPIServices.CreateProduct(productDomainModel)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    handleClose();
  };

  const HandleImage = async (event) => {
    var image = event.target.files[0];
    const formData = new FormData();
    formData.append("image", image);

    await WebAPIServices.UploadImage(formData)
      .then((jsonResponse) => {
        let { imageURL } = jsonResponse.dynamicDomainModel;
        setProductImage(imageURL);
        console.log(imageURL);
      })
      .catch((err) => err);
  };

  const handleEdit = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.GetInvetory(_id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        const {
          _id,
          productName,
          suppliersName,
          price,
          imagePath,
          serialNumber,
          createdBy,
          variation,
          productDescription,
        } = jsonResponse.dynamicDomainModel[0];
        console.log(imagePath);
        setProductImage(imagePath);
        setID(_id);
        setProductName(productName);
        setSuppliersName(suppliersName);
        setProductPrice(price);
        setCreatedBy(createdBy);
        setSerialNumber(serialNumber);
        setProductVariation(variation);
        setProductDescription(productDescription);
        handleShowEdit(true);
      })
      .catch((err) => err);
  };

  const UpdateProduct = async () => {
    var dynamicDomainModel = {
      _id: _id,
      categoryName: "",
      categoryID: categoryId,
      productID: 1000,
      productName: productName,
      productDescription: productDescription,
      productSpecifications: "",
      price: productPrice,
      weight: "",
      imagePath: imageString,
      serialNumber: serialNumber,
      variation: productVariation,
      status: 1,
      productStatus: 1,
      createdDateString: "",
      dateModifiedString: "",
      createdBy: createdBy,
      modifiedBy: user.fullName,
      productTimes: {
        dateCreated: "2022-11-28T04:52:42.038Z",
        dateModified: "2022-11-28T04:52:42.038Z",
        obsoluteDate: "2022-11-28T04:52:42.038Z",
        shipmentStartDate: "2022-11-28T04:52:42.038Z",
        shipmentProcesingDate: "2022-11-28T04:52:42.038Z",
        shipmentEndDate: "2022-11-28T04:52:42.038Z",
      },
    };
    await WebAPIServices.UpdateProduct(dynamicDomainModel)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    handleCloseEdit();
  };

  const TransferStock = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.ProductStatus(_id, 2)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const ReturnStock = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.ProductStatus(_id, 0)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const AddToState = (event) => {
    let _id = event.target.value;
    if (statuses.includes(_id)) {
      let filter = statuses.filter((e) => e !== _id);
      setStatuses(filter);
    } else {
      statuses.push(_id);
    }
  };

  const ActivateOne = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.ProductState(_id, 1)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const ActivateMany = async (event) => {
    await WebAPIServices.ActivateProducts(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const DisableOne = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.ProductState(_id, 0)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeleteProduct = async (event) => {
    let _id = event.target.getAttribute("data-id");
    await WebAPIServices.DeleteProduct(_id)
      .then((jsonResponse) => {
        setLoading(false);
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeleteProducts = async (event) => {
    await WebAPIServices.DeleteProducts(statuses)
      .then((jsonResponse) => {
        setLoading(false);
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
  };

  const DeactivateMany = async () => {
    await WebAPIServices.DeactivateProducts(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const BulkTransfer = async () => {
    await WebAPIServices.ProductStatuses(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const ReturnProducts = async () => {
    await WebAPIServices.ReturnedStatuses(statuses)
      .then((jsonResponse) => {
        refreshState(!refresh);
        console.log(jsonResponse);
      })
      .catch((err) => err);
    setStatuses([]);
  };

  const DeleteProductImage = async (event) => {
    console.log(productImage);
    await WebAPIServices.DeleteProductImage(productImage)
      .then((jsonResponse) => {
        console.log(jsonResponse);
      })
      .catch((err) => err);

    var image = event.target.files[0];
    const formData = new FormData();
    formData.append("image", image);
    var url = EndpointNames.uploadImage;
    await axios
      .post(url, formData)
      .then((jsonResponse) => {
        imageString = jsonResponse.data.dynamicDomainModel.imageURL;
        console.log(imageString);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <div className="d-flex justify-content-end align-items-right mb-3">
              <div className="col-12 col-md-8 col-lg-5">
                <input
                  type="search"
                  placeholder="Start searching any product....."
                  className="form-control mr-3"
                  onChange={async (e) => {
                    let name = e.target.value;
                    if (name === "") {
                      Data();
                    } else {
                      await WebAPIServices.GetProductInInvetoryByName(name)
                        .then((jsonResponse) => {
                          console.log(jsonResponse);
                          if (jsonResponse.returnStatusCode === 200) {
                            setLoading(true);
                            setData(jsonResponse.dynamicDomainModel);
                          } else {
                            setLoading(false);
                          }
                        })
                        .catch((err) => err);
                    }
                  }}
                />
              </div>
            </div>
            <div className="d-flex flex-wrap">
              <button
                className="btn btn-sm bg-warning text-dark mt-2"
                onClick={BulkTransfer}
              >
                {" "}
                <FontAwesomeIcon icon={faTruckArrowRight} />
                Tranfer
              </button>
              <button
                className="btn btn-sm bg-white text-dark mt-2"
                onClick={ReturnProducts}
              >
                {" "}
                <FontAwesomeIcon icon={faTruckLoading} />
                Return
              </button>
              <button
                className="btn btn-sm bg-danger text-white mt-2"
                onClick={DeleteProducts}
              >
                {" "}
                <FontAwesomeIcon icon={faTrashAlt} />
                Delete
              </button>
              <button
                className="btn btn-sm bg-primary text-white mt-2"
                onClick={ActivateMany}
              >
                {" "}
                <FontAwesomeIcon icon={faLightbulb} />
                Activate
              </button>
              <button
                className="btn btn-sm bg-secondary text-white mt-2"
                onClick={DeactivateMany}
              >
                {" "}
                <FontAwesomeIcon icon={faCancel} />
                Deactivate
              </button>
              <small>
                <Form.Select
                  style={{ width: "100%" }}
                  onChange={(event) => {
                    var name = event.target.value;
                    if (name.toLowerCase() === "received") {
                      setStateType(0);
                      refreshState(!refresh);
                    } else if (name.toLowerCase() === "intransit") {
                      setStateType(1);
                      refreshState(!refresh);
                    } else if (name.toLowerCase() === "tranferred") {
                      setStateType(2);
                      refreshState(!refresh);
                    }
                  }}
                >
                  <option>Received</option>
                  <option>InTransit</option>
                  <option>Tranferred</option>
                </Form.Select>
              </small>
              <small>
                <Form.Select
                  style={{ width: "100%" }}
                  onChange={(event) => {
                    var name = event.target.value;
                    if (name.toLowerCase() === "active") {
                      setState(1);
                      refreshState(!refresh);
                      console.log(name);
                    } else if (name.toLowerCase() === "inactive") {
                      setState(0);
                      refreshState(!refresh);
                    }
                  }}
                >
                  <option>Active</option>
                  <option>Inactive</option>
                </Form.Select>
              </small>
            </div>
          </div>
          <div className="p-2 overflow-auto">
            <span>Recent activities</span>
            <table className="table table-responsive text-dark">
              <thead>
                <tr className="text-dark">
                  <th scope="col">#</th>
                  <th scope="col">Product name</th>
                  <th>Quantity</th>
                  <th scope="col">Date created / modified</th>
                  <th scope="col">Status</th>
                  <th scope="col">Modify</th>
                  <th scope="col">Tranfer</th>
                  <th scope="col">State</th>
                  <th scope="col">Delete</th>
                </tr>
              </thead>
              <tbody>
                {loading && (
                  <>
                    {data.map((value) => {
                      const {
                        _id,
                        productCount,
                        productName,
                        productStatus,
                        createdDateString,
                        status,
                      } = value;
                      if (productStatus === stateType && status === state) {
                        return (
                          <>
                            <tr key={_id}>
                              <td>
                                <input
                                  type="checkbox"
                                  value={_id}
                                  onChange={(e) => AddToState(e)}
                                />
                              </td>
                              <td>{productName}</td>
                              <td>{productCount}</td>
                              <td>{createdDateString}</td>
                              <td>
                                {status ? (
                                  <>
                                    <FontAwesomeIcon
                                      icon={faCircleDot}
                                      className="text-success"
                                    />
                                  </>
                                ) : (
                                  <>
                                    <FontAwesomeIcon
                                      icon={faCancel}
                                      className="text-danger"
                                    />
                                  </>
                                )}
                              </td>
                              <td>
                                {" "}
                                <Nav.Link
                                  href="#"
                                  data-id={_id}
                                  onClick={(e) => handleEdit(e)}
                                >
                                  <FontAwesomeIcon icon={faPencil} />
                                  Modify
                                </Nav.Link>
                              </td>
                              <td>
                                {productStatus === 0 ? (
                                  <>
                                    <Nav.Link
                                      href="#"
                                      data-id={_id}
                                      onClick={(e) => TransferStock(e)}
                                    >
                                      <FontAwesomeIcon
                                        icon={faTruckArrowRight}
                                      />
                                      Tranfer
                                    </Nav.Link>
                                  </>
                                ) : (
                                  <>
                                    <Nav.Link
                                      href="#"
                                      data-id={_id}
                                      onClick={(e) => ReturnStock(e)}
                                    >
                                      <FontAwesomeIcon icon={faTruckLoading} />
                                      Return
                                    </Nav.Link>
                                  </>
                                )}
                              </td>
                              {status ? (
                                <>
                                  <td>
                                    <Nav.Link
                                      href="#"
                                      data-id={_id}
                                      onClick={(e) => DisableOne(e)}
                                    >
                                      <FontAwesomeIcon
                                        icon={faCancel}
                                        className="text-danger"
                                      />
                                      Deactivate
                                    </Nav.Link>
                                  </td>
                                </>
                              ) : (
                                <>
                                  <td>
                                    <Nav.Link
                                      href="#"
                                      data-id={_id}
                                      onClick={(e) => ActivateOne(e)}
                                    >
                                      <FontAwesomeIcon
                                        icon={faCircleDot}
                                        className="text-success"
                                      />
                                      Activate
                                    </Nav.Link>
                                  </td>
                                </>
                              )}
                              <td>
                                <Nav.Link
                                  href="#"
                                  data-id={_id}
                                  onClick={(e) => DeleteProduct(e)}
                                >
                                  <FontAwesomeIcon icon={faTrashAlt} />
                                  Delete
                                </Nav.Link>
                              </td>
                            </tr>
                          </>
                        );
                      }
                    })}
                  </>
                )}
              </tbody>
            </table>
          </div>
          <Modal show={show} onHide={handleClose}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Create Product(s)</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Category</Form.Label>
                  <Form.Select onClick={(e) => setCategoryID(e.target.value)}>
                    {categories.map((value) => {
                      let { _id, categoryName } = value;
                      return (
                        <>
                          <option value={_id}>{categoryName}</option>
                        </>
                      );
                    })}
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Product Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product name"
                    onKeyUp={(e) => setProductName(e.target.value)}
                    name="productName"
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product price"
                    onKeyUp={(e) => setProductPrice(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Serial Number</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Serial Number"
                    onKeyUp={(e) => setSerialNumber(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Variation</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Product Variation"
                    onKeyUp={(e) => setProductVariation(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Description</Form.Label>
                  <textarea
                    rows="4"
                    className="form-control"
                    onKeyUp={(e) => setProductDescription(e.target.value)}
                    placeholder="None"
                  ></textarea>
                </Form.Group>
                <Form.Group
                  className="mb-3"
                  controlId="formBasicVariation"
                  onChange={(e) => HandleImage(e)}
                >
                  <Form.Label>Image</Form.Label>
                  <input
                    type="file"
                    className="form-control"
                    placeholder="Enter product variation"
                  />
                </Form.Group>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="warning" onClick={handleClose}>
                  Close
                </Button>
                <Button variant="dark" onClick={(e) => handleSubmit(e)}>
                  Create Product
                  <FontAwesomeIcon icon={faBoxOpen} />
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
          <Modal show={edit} onHide={handleCloseEdit}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Edit Product(s)</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Category</Form.Label>
                  <Form.Select onClick={(e) => setCategoryID(e.target.value)}>
                    {categories.map((value) => {
                      let { _id, categoryName } = value;
                      return (
                        <>
                          <option value={_id}>{categoryName}</option>
                        </>
                      );
                    })}
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Product Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product name"
                    onKeyUp={(e) => setProductName(e.target.value)}
                    name="productName"
                    defaultValue={productName}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Suppliers Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter suppliers name"
                    onKeyUp={(e) => setSuppliersName(e.target.value)}
                    name="suppliersName"
                    defaultValue={suppliersName}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product price"
                    onKeyUp={(e) => setProductPrice(e.target.value)}
                    defaultValue={productPrice}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Serial Number</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Serial Number"
                    onKeyUp={(e) => setSerialNumber(e.target.value)}
                    defaultValue={serialNumber}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Variation</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Product Variation"
                    onKeyUp={(e) => setProductVariation(e.target.value)}
                    defaultValue={productVariation}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Description</Form.Label>
                  <textarea
                    rows="4"
                    className="form-control"
                    onKeyUp={(e) => setProductDescription(e.target.value)}
                    placeholder="None"
                    defaultValue={productDescription}
                  ></textarea>
                </Form.Group>
                <Form.Group
                  className="mb-3"
                  controlId="formBasicVariation"
                  onChange={(e) => DeleteProductImage(e)}
                >
                  <Form.Label>Image</Form.Label>
                  <input
                    type="file"
                    className="form-control"
                    placeholder="Enter product variation"
                  />
                </Form.Group>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="warning" onClick={handleCloseEdit}>
                  Close
                </Button>
                <Button variant="dark" onClick={UpdateProduct}>
                  Update Product
                  <FontAwesomeIcon icon={faBoxOpen} />
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
        </div>
      </div>
    </>
  );
};

export default ReceivingStock;
