import { Form, Modal } from "react-bootstrap";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import Button from "react-bootstrap/Button";
import WebAPIServices from "../serv/apiservices";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [logi, setLogi] = useState(false);
  const [validated, isValidated] = useState(true);

  let navigate = useNavigate();

  const handleSubmit = async (e) => {
    var profileDomainModel = {
      _id: "",
      FirstName: "",
      LastName: "",
      FullName: "",
      Salt: "",
      Password: password,
      Email: email,
      Role: 1,
      DateCreated: "2022-11-28T04:52:42.038Z",
      DateModified: "2022-11-28T04:52:42.038Z",
      Status: 1,
    };
    await WebAPIServices.LoginUser(profileDomainModel)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          isValidated(true);
          localStorage.setItem(
            "user",
            JSON.stringify(jsonResponse.dynamicDomainModel)
          );
          if (jsonResponse.dynamicDomainModel.role === 0) {
            navigate("/Live/Admin/Dashboard");
          } else if (jsonResponse.dynamicDomainModel.role === 1) {
            navigate("/Live/Sellers/Portal");
          }
        } else if (jsonResponse.returnStatusCode === 204) {
          isValidated(false);
          alert("Invalid username or password");
          return (
            <>
              <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="warning"
              />
              Invalid Username or password
              <ToastContainer />;
            </>
          );
        } else if (jsonResponse.returnStatusCode === 500) {
          isValidated(false);
          toast("Something went wrong");
          return (
            <>
              <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="danger"
              />
              Something went wrong internally, Please try again later
              <ToastContainer />;
            </>
          );
        }
      })
      .catch((err) => err);
    e.PreventDefault();
  };
  return (
    <>
      <div className="d-flex justify-content-center align-items-center">
        <div>
          <div
            className="position-absolute top-50 start-50 translate-middle p-5 shadow"
            style={{ width: "400px" }}
          >
            {!validated && (
              <>
                <div className="border-2 p-2 mb-3 text-danger">
                  <p className="text-center">Invalid username or password!!!</p>
                </div>
              </>
            )}
            <div className="d-flex justify-content-center align-items-center mb-4">
              <img
                src="/images/insolar.png"
                alt=""
                width="130px"
                height="45px"
              />
            </div>
            <Form>
              <Modal.Header>
                <Modal.Title>Login User</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>userName</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter firstname"
                    onKeyUp={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicUser">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type="password"
                    placeholder="Enter password"
                    onKeyUp={(e) => setPassword(e.target.value)}
                  />
                </Form.Group>
                <div className="d-grid">
                  <button
                    type="button"
                    className="btn btn-primary"
                    onClick={(e) => handleSubmit(e)}
                  >
                    Login
                  </button>
                </div>
              </Modal.Body>
            </Form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
