import {
  faArchive,
  faBoxArchive,
  faBoxesAlt,
  faBoxesStacked,
  faCancel,
  faChainBroken,
  faChevronDown,
  faChevronLeft,
  faChevronRight,
  faChevronUp,
  faDashboard,
  faFileArchive,
  faFileInvoiceDollar,
  faHome,
  faList,
  faListUl,
  faMoneyBill,
  faRemove,
  faSignOut,
  faTableList,
  faTruck,
  faUser,
  faUserGroup,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Nav } from "react-bootstrap";
import "../../App.css";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

const SidePane = () => {
  const [block1, openBlock1] = useState(false);
  const [block2, openBlock2] = useState(false);
  const [block3, openBlock3] = useState(false);
  const [block4, openBlock4] = useState(false);
  let navigate = useNavigate();
  return (
    <>
      <div className="col-2 bg-success h-100 d-none d-lg-block overflow-auto">
        <div className="text-white text-center p-3 text-muted">
          <div className="bg-white p-2">
            <img
              src="/images/insolar.png"
              alt="insolar"
              width="100%"
              height="100%"
            />
          </div>
        </div>
        <Nav className="d-block">
          <Nav.Link
            className="text-white"
            onClick={() => navigate("/Live/Admin/Dashboard")}
            style={{ fontSize: "15px", cursor: "pointer" }}
          >
            <Nav.Item>
              <FontAwesomeIcon icon={faDashboard} /> Dashboard
            </Nav.Item>
          </Nav.Link>
          <Nav.Link
            className="text-white"
            onClick={() => navigate("/Live/Admin/Supplier")}
          >
            <Nav.Item>
              <FontAwesomeIcon icon={faTruck} /> Suppliers
            </Nav.Item>
          </Nav.Link>
          <Nav.Link
            className="text-white d-flex justify-content-between w-100"
            onClick={() => openBlock1(!block1)}
          >
            <div>
              <Nav.Item>
                <FontAwesomeIcon icon={faTableList} /> Product Info
              </Nav.Item>
            </div>
            <div>
              {block1 ? (
                <FontAwesomeIcon icon={faChevronUp} />
              ) : (
                <FontAwesomeIcon icon={faChevronRight} />
              )}
            </div>
          </Nav.Link>
          {block1 && (
            <>
              <Nav className="text-white">
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/CreateCategory")}
                >
                  Categories
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/DoubleEntry")}
                >
                  Double Entry
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/PurchaseOrder")}
                >
                  Purchase order
                </span>
                <span
                  className="px-4"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/InvetoryList")}
                >
                  Products
                </span>
              </Nav>
            </>
          )}
          <Nav.Link
            className="text-white d-flex justify-content-between w-100"
            onClick={() => openBlock2(!block2)}
          >
            <div>
              <Nav.Item>
                <FontAwesomeIcon icon={faArchive} /> Stock take
              </Nav.Item>
            </div>
            <div>
              {block2 ? (
                <FontAwesomeIcon icon={faChevronUp} />
              ) : (
                <FontAwesomeIcon icon={faChevronRight} />
              )}
            </div>
          </Nav.Link>
          {block2 && (
            <>
              <Nav className="text-white">
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/StockTake")}
                >
                  Stock take
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/StockTakeReport")}
                >
                  Stock take sheet
                </span>
                <span
                  className="px-4"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/RecieveStock")}
                >
                  Price change
                </span>
              </Nav>
            </>
          )}
          <Nav.Link className="text-white">
            <Nav.Item>
              <FontAwesomeIcon icon={faMoneyBill} /> Expenses
            </Nav.Item>
          </Nav.Link>
          <Nav.Link className="text-white">
            <Nav.Item>
              <FontAwesomeIcon icon={faRemove} /> Writeoff
            </Nav.Item>
          </Nav.Link>
          <Nav.Link
            className="text-white d-flex justify-content-between w-100"
            onClick={() => openBlock3(!block3)}
          >
            <div>
              <Nav.Item>
                <FontAwesomeIcon icon={faArchive} /> Anual Reports
              </Nav.Item>
            </div>
            <div>
              {block3 ? (
                <FontAwesomeIcon icon={faChevronUp} />
              ) : (
                <FontAwesomeIcon icon={faChevronRight} />
              )}
            </div>
          </Nav.Link>
          {block3 && (
            <>
              <Nav className="text-white">
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/TotalSales")}
                >
                  Todays sales
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/Sales")}
                >
                  Sales Report
                </span>
                <span
                  className="px-4"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/MostSelling")}
                >
                  Most selling product
                </span>
              </Nav>
            </>
          )}
          <Nav.Link
            className="text-white d-flex justify-content-between w-100"
            onClick={() => openBlock4(!block4)}
          >
            <div>
              <Nav.Item>
                <FontAwesomeIcon icon={faArchive} /> General reports
              </Nav.Item>
            </div>
            <div>
              {block4 ? (
                <FontAwesomeIcon icon={faChevronUp} />
              ) : (
                <FontAwesomeIcon icon={faChevronRight} />
              )}
            </div>
          </Nav.Link>
          {block4 && (
            <>
              <Nav className="text-white">
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/ProductTrait")}
                >
                  Product trail report
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/BusinessLead")}
                >
                  Business lead
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/AccountStatement")}
                >
                  Account statement
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/ProfitOrLoss")}
                >
                  Profit or Loss
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/PurchaseReport")}
                >
                  Purchase report
                </span>
                <span
                  className="px-4 pb-1"
                  style={{ fontSize: "15px", cursor: "pointer" }}
                  onClick={() => navigate("/Live/Admin/Payout")}
                >
                  Payout
                </span>
              </Nav>
            </>
          )}
          <Nav.Link
            className="text-white"
            onClick={() => navigate("/Live/Admin/Profiles")}
          >
            <Nav.Item>
              <FontAwesomeIcon icon={faUserGroup} />
              Profiles
            </Nav.Item>
          </Nav.Link>
          <Nav.Link
            className="text-white"
            onClick={() => {
              navigate("/Login");
              localStorage.removeItem("user");
            }}
          >
            <Nav.Item>
              <FontAwesomeIcon icon={faSignOut} /> Signout
            </Nav.Item>
          </Nav.Link>
        </Nav>
      </div>
    </>
  );
};

export default SidePane;
