import HeaderTemplate from "../../page_templates/header_template";
import { useState } from "react";
import {
  faAdd,
  faBoxOpen,
  faCancel,
  faDownload,
  faFile,
  faLightbulb,
  faPencil,
  faPrint,
  faThumbsUp,
  faTrashAlt,
  faTruckArrowRight,
  faTruckLoading,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SidePane from "../../page_templates/side_pane";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { Form } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import WebAPIServices from "../../../serv/apiservices";
import { useEffect } from "react";

const InvetoryList = () => {
  const [refresh, setRefresh] = useState(false);
  const [id, setIDs] = useState([]);
  const [data, setData] = useState([]);
  const [state, setState] = useState(false);
  const [preview, setPreview] = useState(false);
  const [documentPreview, setDocumentPreview] = useState([]);
  const [_id, setID] = useState("");
  useEffect(() => {
    Data();
  }, [refresh]);
  const Data = async () => {
    await WebAPIServices.GetFullInvetory()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
          setState(true);
        } else {
          setState(false);
        }
      })
      .catch((err) => err);
  };

  const AddToState = (_id) => {
    if (id.includes(_id)) {
      let filter = id.filter((e) => e !== _id);
      setIDs(filter);
    } else {
      id.push(_id);
    }
    console.log(id);
  };

  const ApproveProduct = async (productID) => {
    await WebAPIServices.ApproveProduct(productID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const ApproveProducts = async () => {
    await WebAPIServices.ApproveProducts(id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const GetProduct = async (_id) => {
    await WebAPIServices.GetInvetory(_id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setDocumentPreview(jsonResponse.dynamicDomainModel);
          setID(_id);
          setPreview(true);
        }
      })
      .catch((err) => err);
  };

  const GeneratePDF = async (ProductID) => {
    await WebAPIServices.GeneratePdf(ProductID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          window.location.href = jsonResponse.dynamicDomainModel.documentUrl;
        }
      })
      .catch((err) => err);
  };

  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <div className="d-flex justify-content-end align-items-right mb-3">
              <div className="col-12 col-md-8 col-lg-5">
                <input
                  type="search"
                  placeholder="Start searching any product....."
                  className="form-control mr-3"
                  onChange={async (e) => {
                    let name = e.target.value;
                    if (name === "") {
                      Data();
                    } else {
                      await WebAPIServices.GetProductInInvetoryByName(name)
                        .then((jsonResponse) => {
                          console.log(jsonResponse);
                          if (jsonResponse.returnStatusCode === 200) {
                            setState(true);
                            setData(jsonResponse.dynamicDomainModel);
                          } else {
                            setState(false);
                          }
                        })
                        .catch((err) => err);
                    }
                  }}
                />
              </div>
            </div>
            <div className="p-2 overflow-auto">
              <span>Recent activities</span>
              <table className="table table-responsive text-dark">
                <thead>
                  <tr className="text-dark">
                    <th scope="col">#</th>
                    <th scope="col">Product name</th>
                    <th>Category</th>
                    <th scope="col">Date created / modified</th>
                    <th scope="col">Preview</th>
                    <th scope="col">Download</th>
                  </tr>
                </thead>
                <tbody>
                  {state && (
                    <>
                      {data.map((value) => {
                        let {
                          _id,
                          productID,
                          productName,
                          categoryName,
                          createdDateString,
                        } = value;
                        return (
                          <>
                            <tr key={_id}>
                              <td>{productID}</td>
                              <td>{productName}</td>
                              <td>{categoryName}</td>
                              <td>{createdDateString}</td>
                              <td>
                                <Nav.Link
                                  href="#"
                                  data-id={_id}
                                  onClick={() => {
                                    GetProduct(_id);
                                  }}
                                >
                                  <FontAwesomeIcon icon={faFile} />
                                  Preview
                                </Nav.Link>
                              </td>
                              <td>
                                <Nav.Link
                                  href="#"
                                  data-id={_id}
                                  onClick={() => GeneratePDF(_id)}
                                >
                                  <FontAwesomeIcon icon={faDownload} />
                                  Download
                                </Nav.Link>
                              </td>
                            </tr>
                          </>
                        );
                      })}
                    </>
                  )}
                </tbody>
              </table>
            </div>
            <Modal show={preview} onHide={() => setPreview(!preview)}>
              <Form>
                <Modal.Header closeButton>
                  <Modal.Title>Product preview</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  {documentPreview.map((value) => {
                    let {
                      _id,
                      productName,
                      price,
                      productDescription,
                      serialNumber,
                      createdDateString,
                    } = value;
                    return (
                      <>
                        <div className="p-3" key={_id}>
                          <span className="d-flex justify-content-between">
                            <p className="fw-bold">Product Name</p>
                            <p>{productName}</p>
                          </span>
                          <hr />
                          <span className="d-flex justify-content-between">
                            <p className="fw-bold">Serial Number</p>
                            <p>{serialNumber}</p>
                          </span>
                          <hr />
                          <span className="d-flex justify-content-between">
                            <p className="fw-bold">Product Description</p>
                            <p>{productDescription}</p>
                          </span>
                          <hr />
                          <span className="d-flex justify-content-between">
                            <p className="fw-bold">Created Date</p>
                            <p>{createdDateString}</p>
                          </span>
                          <hr />
                          <span className="d-flex justify-content-between">
                            <h5>Total Price</h5>
                            <h4 className="fw-bold">${price}</h4>
                          </span>
                        </div>
                      </>
                    );
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button
                    variant="primary"
                    onClick={() => setPreview(!preview)}
                  >
                    <FontAwesomeIcon icon={faPrint} />
                    Print
                  </Button>
                  <Button
                    variant="success"
                    onClick={() => {
                      GeneratePDF(_id);
                      setPreview(!preview);
                    }}
                  >
                    <FontAwesomeIcon icon={faDownload} />
                    Download
                  </Button>
                </Modal.Footer>
              </Form>
            </Modal>
          </div>
        </div>
      </div>
    </>
  );
};

export default InvetoryList;
