import HeaderTemplate from "../../../page_templates/header_template";
import SidePane from "../../../page_templates/side_pane";
import { Row, Col } from "react-bootstrap";
import { useState } from "react";
import WebAPIServices from "../../../../serv/apiservices";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretUp, faEye } from "@fortawesome/free-solid-svg-icons";
import { Nav } from "react-bootstrap";

const ProductTraitReport = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [sales, setSales] = useState([]);
  const [modified, setModified] = useState([]);
  const [purchases, setPurchases] = useState([]);

  const Data = async () => {
    await WebAPIServices.TraitReport(startDate, endDate)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setPurchases(jsonResponse.dynamicDomainModel.purchases);
          setSales(jsonResponse.dynamicDomainModel.sales);
          setModified(jsonResponse.dynamicDomainModel.modified);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <h3>Product Trail Report</h3>
            <div className="d-flex jsutify-content-between col-8">
              <div>
                <div className="input-group">
                  StartDate:
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setStartDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <div className="input-group">
                  EndDate:
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setEndDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <button
                  className="btn btn-sm btn-primary"
                  onClick={() => Data()}
                >
                  Generate report
                </button>
              </div>
            </div>
            <Row lg={2} className="col-12 flex-direction-column">
              <Col lg={12}>
                <h4>Modified Items</h4>
                <div>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Product</th>
                        <th>Date</th>
                        <th>Preview</th>
                      </tr>
                    </thead>
                    <tbody>
                      {sales.length > 0 && (
                        <>
                          {modified.map((d, key) => {
                            return (
                              <>
                                <tr>
                                  <th>{key + 1}</th>
                                  {d.orderDetails.map((item) => {
                                    return (
                                      <>
                                        <td>{item.productName}</td>
                                        <td>{d.orderedDateString}</td>
                                      </>
                                    );
                                  })}
                                  <td>
                                    <Nav.Item style={{ cursor: "pointer" }}>
                                      <FontAwesomeIcon icon={faEye} />
                                    </Nav.Item>
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </Col>
              <Col lg={12}>
                <h4>Sales</h4>
                <div>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Product</th>
                        <th>Date</th>
                        <th>Preview</th>
                      </tr>
                    </thead>
                    <tbody>
                      {sales.length > 0 && (
                        <>
                          {sales.map((d, key) => {
                            return (
                              <>
                                <tr>
                                  <th>{key + 1}</th>
                                  {d.orderDetails.map((item) => {
                                    return (
                                      <>
                                        <td>{item.productName}</td>
                                        <td>{d.orderedDateString}</td>
                                      </>
                                    );
                                  })}
                                  <td>
                                    <Nav.Item style={{ cursor: "pointer" }}>
                                      <FontAwesomeIcon icon={faEye} />
                                    </Nav.Item>
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </Col>
              <Col lg={12}>
                <h4>Purchases</h4>
                <div>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Supplier</th>
                        <th>Sales Rep</th>
                        <th>Product</th>
                        <th>Date</th>
                        <th>Preview</th>
                      </tr>
                    </thead>
                    <tbody>
                      {purchases.length > 0 && (
                        <>
                          {purchases.map((item, key) => {
                            return (
                              <>
                                <tr>
                                  <th>{key + 1}</th>
                                  <td>{item.supplier.supplierName}</td>
                                  <td>{item.salesPerson.fullName}</td>
                                  <td>{item.productDomainModel.productName}</td>
                                  <td>{item.purchaseDateString}</td>
                                  <td>
                                    <Nav.Item style={{ cursor: "pointer" }}>
                                      <FontAwesomeIcon icon={faEye} />
                                    </Nav.Item>
                                  </td>
                                </tr>
                              </>
                            );
                          })}
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductTraitReport;
