import HeaderTemplate from "../../page_templates/header_template";
import {
  faAdd,
  faBoxOpen,
  faPencil,
  faTrashAlt,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState, useEffect } from "react";
import { Nav, Form } from "react-bootstrap";
import { Modal } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import SidePane from "../../page_templates/side_pane";
import WebAPIServices from "../../../serv/apiservices";
const Category = () => {
  const [add, addModal] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [categoryId, setCategoryID] = useState("");
  const [categoryName, setCategoryName] = useState("");
  const [edit, editModal] = useState(false);

  const CreateCategory = async () => {
    let categoryDomainModel = {
      _id: "",
      CategoryName: categoryName,
      aging: 1,
    };
    await WebAPIServices.CreateCategory(categoryDomainModel)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const GetCategory = async (categoryID) => {
    await WebAPIServices.GetCategory(categoryID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        let { _id, categoryName } = jsonResponse.dynamicDomainModel;
        setCategoryID(_id);
        setCategoryName(categoryName);
        editModal(!edit);
      })
      .catch((err) => err);
  };

  const UpdateCategory = async () => {
    let categoryDomainModel = {
      _id: categoryId,
      CategoryName: categoryName,
    };
    await WebAPIServices.UpdateCategory(categoryDomainModel)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const DeleteCategory = async (categoryID) => {
    await WebAPIServices.DeleteCategory(categoryID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  useEffect(() => {
    Data();
  }, [refresh]);

  const Data = async () => {
    await WebAPIServices.GetCategories()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
          setLoading(true);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };

  return (
    <>
      <div
        className="position-absolute bg-primary overflow-hidden"
        style={{ width: "100%", height: "100%" }}
      >
        <div className="col-12 d-flex bg-warning" style={{ height: "100%" }}>
          <SidePane />
          <div className="bg-white col-12 col-lg-10">
            <HeaderTemplate />
            <div className="p-3">
              <button
                className="btn btn-primary btn-sm"
                onClick={() => addModal(!add)}
              >
                <FontAwesomeIcon icon={faAdd} />
                Create category
              </button>
              <table className="table">
                <thead>
                  <tr>
                    <th>CategoryName</th>
                    <th>Update</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {loading && (
                    <>
                      {data.map((value) => {
                        const { _id, categoryName } = value;
                        return (
                          <>
                            <tr key={_id}>
                              <td>{categoryName}</td>
                              <td>
                                <Nav.Link
                                  href="#"
                                  data-id={_id}
                                  onClick={(e) => {
                                    let categoryID =
                                      e.currentTarget.getAttribute("data-id");
                                    GetCategory(categoryID);
                                  }}
                                >
                                  <FontAwesomeIcon icon={faPencil} />
                                  update
                                </Nav.Link>
                              </td>
                              <td>
                                <Nav.Link
                                  href="#"
                                  className="text-danger"
                                  data-id={_id}
                                  onClick={() => DeleteCategory(_id)}
                                >
                                  <FontAwesomeIcon icon={faTrashAlt} />
                                  delete
                                </Nav.Link>
                              </td>
                            </tr>
                          </>
                        );
                      })}
                    </>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <Modal show={add} onHide={() => addModal(!add)}>
          <Form>
            <Modal.Header closeButton>
              <Modal.Title>Create Category</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="modal-body">
                <div className="form-group">
                  <label htmlFor="productname" className="mb-3">
                    CategoryName
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="productname"
                    placeholder="Enter category name"
                    onKeyUp={(e) => setCategoryName(e.target.value)}
                  />
                </div>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="warning" onClick={() => addModal(!add)}>
                Close
              </Button>
              <Button
                variant="dark"
                onClick={() => {
                  addModal(!add);
                  CreateCategory();
                }}
              >
                Create Category
                <FontAwesomeIcon icon={faBoxOpen} />
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
        <Modal show={edit} onHide={() => editModal(!edit)}>
          <Form>
            <Modal.Header closeButton>
              <Modal.Title>Create Category</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="modal-body">
                <div className="form-group">
                  <label htmlFor="productname" className="mb-3">
                    CategoryName
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="productname"
                    placeholder="Enter category name"
                    onKeyUp={(e) => setCategoryName(e.target.value)}
                    defaultValue={categoryName}
                  />
                </div>
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="warning" onClick={() => editModal(!edit)}>
                Close
              </Button>
              <Button
                variant="dark"
                onClick={() => {
                  editModal(!edit);
                  UpdateCategory();
                }}
              >
                Update Category
                <FontAwesomeIcon icon={faBoxOpen} />
              </Button>
            </Modal.Footer>
          </Form>
        </Modal>
      </div>
    </>
  );
};

export default Category;
