import SidePane from "../../page_templates/side_pane";
import HeaderTemplate from "../../page_templates/header_template";
import { useState, useEffect } from "react";
import WebAPIServices from "../../../serv/apiservices";

const StockTake = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    Data();
  }, []);
  const Data = async () => {
    await WebAPIServices.StockTake()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3 col-12">
            <div className="d-flex justify-content-between col-12">
              <div>
                <h3>Stock take</h3>
              </div>
              <div className="col-4">
                <input
                  type="search"
                  className="form-control"
                  placeholder="Search your category here"
                  onKeyUp={async (e) => {
                    let name = e.target.value;
                    if (name === "") {
                      Data();
                    } else {
                      await WebAPIServices.GetCategoryInStock(name)
                        .then((jsonResponse) => {
                          console.log(jsonResponse);
                          if (jsonResponse.returnStatusCode === 200) {
                            setData(jsonResponse.dynamicDomainModel);
                          }
                        })
                        .catch((err) => err);
                    }
                  }}
                />
              </div>
            </div>
            <table className="table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Category</th>
                  <th>Products</th>
                </tr>
              </thead>
              <tbody>
                {data.map((item, key) => {
                  let value = item.value;
                  let arr = value.split("|");
                  return (
                    <>
                      <tr>
                        <td>{key + 1}</td>
                        <td>{arr[0]}</td>
                        <td>{arr[1]}</td>
                      </tr>
                    </>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default StockTake;
