import { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { Nav, Form, NavItem } from "react-bootstrap";
import { Modal } from "react-bootstrap";
import {
  faBackspace,
  faCalculator,
  faCancel,
  faCashRegister,
  faComputer,
  faMoneyBill,
  faMoneyBillTrendUp,
  faPrint,
  faRemove,
  faSignOut,
  faTrashAlt,
  faUser,
  faUserGear,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import WebAPIServices from "../../serv/apiservices";
const SellersPortal = () => {
  const [show, setShow] = useState(false);
  const [productState, setProductState] = useState(false);
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [printContainer, setPrintContainer] = useState(false);
  const [bar, setBar] = useState(false);
  const [sValue, setValue] = useState("");
  let [order, setOrder] = useState([]);
  const [productPrice, setProductPrice] = useState(0);
  const [change, setChange] = useState(0);
  const [sign, setSign] = useState("");
  const [paymentType, setPaymentType] = useState("USD");
  const [status, setStatus] = useState(false);
  const [rate, setRate] = useState(false);
  const [zwl, zwlRate] = useState(1);
  const [sales, setSales] = useState([]);
  const [categoryIds, setCategoryIds] = useState([]);
  const [productIds, setProductIds] = useState([]);
  const [todo, setTodo] = useState(false);
  const [reprint, setReprint] = useState(false);
  const [orderDetails, setOrderDetails] = useState([]);
  const [pModal, setPModal] = useState(false);
  const [fModal, setFModal] = useState(false);
  const [dModal, setDModal] = useState(false);
  const [finances, setFinances] = useState([]);
  const [dayEnd, setDayEnd] = useState([]);
  const [orderNumber, setOrderNumber] = useState(0);
  const [sum, setSum] = useState(0);
  let user = JSON.parse(localStorage.getItem("user"));
  const [cashierID, setCashierID] = useState(user._id);
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");

  const ChangePassword = async () => {};

  let navigate = useNavigate();

  const UserEvent = (e) => {
    let symbol = e.currentTarget.children[0].innerHTML;
    if (symbol === "*") {
      document.querySelector(".box").focus();
    } else if (symbol === "+") {
      document.querySelector(".box").focus();
    } else if (symbol === "/") {
      document.querySelector(".box").focus();
    } else if (symbol === "-") {
      document.querySelector(".box").focus();
    } else if (symbol === ".") {
      setSign(sign + symbol.trim());
      document.querySelector(".box").focus();
      document.querySelector(".box").value += symbol.trim();
    } else {
      setSign(sign + symbol.trim());
      document.querySelector(".box").focus();
      document.querySelector(".box").value += symbol.trim();
    }
  };

  const BackSpace = (e) => {
    document.querySelector(".box").focus();
    let strLen = document.querySelector(".box").value.length;
    if (document.querySelector(".box").value.length > 0) {
      let subStr = document
        .querySelector(".box")
        .value.substring(0, strLen - 1);
      setSign(subStr);
      document.querySelector(".box").value = subStr;
    }
  };
  const HandlePayment = () => {
    let owe, paid, balance;
    paid = document.querySelector(".box").value.trim();
    owe = productPrice;
    balance = paid - owe;
    setChange(balance);
    if (owe > paid) {
      alert("Amount payable is less than amount owed.....");
      document.getElementById("status").className = "text-danger";
      setStatus(false);
      setChange(0);
    } else {
      document.getElementById("status").className = "text-success";
      setStatus(true);
      handleClose();
    }
  };
  const HandleProductLoad = (_id) => {
    const ProductExist = order.find((item) => item._id === _id);
    if (ProductExist) {
      setProductPrice(
        rate
          ? productPrice - ProductExist.price
          : productPrice - ProductExist.price * zwl
      );
      setOrder(
        order.map((item) =>
          item._id === _id
            ? {
                ...ProductExist,
                data: item.data,
                quantity: ProductExist.quantity - 1,
                price: item.price,
                totalPrice: ProductExist.totalPrice - item.price,
              }
            : item
        )
      );
    }
    order.map((item) => {
      if (item._id === _id) {
        if (ProductExist.quantity > 1) {
        } else {
          let filter = order.filter((a) => a._id !== _id);
          setOrder(filter);
        }
      }
    });
  };
  const SalesMaster = async () => {
    let orderNumber = 0;
    order.map((item) => {
      let { _id, productName, productID, categoryID, imagePath } = item.data;
      categoryIds.push(categoryID);
      productIds.push(productID.toString());
      sales.push({
        _id: _id,
        ProductName: productName,
        Quantity: item.quantity,
        Price: item.totalPrice,
        ImagePath: imagePath,
      });
    });
    await WebAPIServices.GetOrderNumber()
      .then(async (jsonResponse) => {
        orderNumber = jsonResponse;
        let salesDomainModel = {
          _id: "",
          CategoryIDs: categoryIds,
          ProductIDs: productIds,
          OrderDetails: sales,
          Cashier: user.fullName,
          CashierID: user._id,
          Branch: "",
          OrderNumber: orderNumber,
          OrderedDate: "2022-11-28T04:52:42.038Z",
          OrderedDateString: "",
          CustomerDetails: {},
          PaymentType: paymentType === "USD" ? 0 : 1,
          Rate: zwl,
        };
        await WebAPIServices.SalesMaster(salesDomainModel)
          .then((jsonResponse) => {
            console.log(jsonResponse);
            setTimeout(() => {
              setCategoryIds([]);
              setProductIds([]);
              setOrder([]);
              setSales([]);
              setPrintContainer(false);
              setProductPrice(0);
              setSign("");
              setStatus(false);
              document.getElementById("status").className = "text-danger";
            }, 2500);
          })
          .catch((err) => err);

        console.log(salesDomainModel);
      })
      .catch((err) => err);
  };
  let ref = useRef();
  return (
    <>
      <div className="pos-f-t d-none">
        <nav className="navbar navbar-dark bg-dark d-flex justify-content-between">
          <h3 className="text-white">Hi User</h3>
          <Nav>
            <Nav.Link href="#" className="text-white d-flex">
              <FontAwesomeIcon icon={faComputer} />
              <h6>POS</h6>
            </Nav.Link>
            <Nav.Link href="#" className="text-white d-flex">
              <FontAwesomeIcon icon={faMoneyBillTrendUp} />
              <h6>Finances</h6>
            </Nav.Link>
            <Nav.Link href="#" className="text-white d-flex">
              <FontAwesomeIcon icon={faUser} />
              <h6>Profile</h6>
            </Nav.Link>
            <Nav.Link href="#" className="text-white d-flex">
              <FontAwesomeIcon icon={faSignOut} />
              <h6>Logout</h6>
            </Nav.Link>
          </Nav>
        </nav>
      </div>
      <div
        className="position-absolute col-12 d-flex justify-content-around align-items-center bg-success"
        style={{ width: "100%", height: "100%" }}
      >
        <div
          className="bg-white col-7 overflow-auto shadow"
          style={{ height: "95%" }}
        >
          <div className="p-2 text-uppercase font-italic">
            <div className="d-flex justify-content-between">
              <div>
                <h5>Your order</h5>
                <h5>Subtotal: ${rate ? productPrice * zwl : productPrice}</h5>
                <h5>Change: ${change}</h5>
                <h6 className="text-danger" id="status">
                  Status: {status ? "paid" : "not paid"}
                </h6>
              </div>
              <div>
                <img
                  src="/images/insolar.png"
                  alt=""
                  width="120px"
                  height="40px"
                />
              </div>
              <div>
                <button
                  className="btn btn-dark btn-lg"
                  onClick={() => setTodo(true)}
                >
                  <FontAwesomeIcon icon={faUserGear} />
                </button>
              </div>
            </div>
            <div className="d-flex justify-content-between">
              <h5>
                <i>Payment method:</i>{" "}
              </h5>
              <small>
                <Form.Select
                  onChange={(e) => {
                    let value = e.target.value;
                    setPaymentType(value);
                    if (paymentType === "ZWL") {
                      setProductPrice(productPrice / zwl);
                      setRate(false);
                      zwlRate(1);
                    } else {
                      setShow(!show);
                      setRate(true);
                    }
                  }}
                >
                  <option value="USD">USD</option>
                  <option value="ZWL">ZWL</option>
                </Form.Select>
              </small>
            </div>
          </div>
          <div className="d-flex align-items-center justify-content-center pb-3">
            <button
              className="btn btn-primary"
              onClick={() => {
                setRate(false);
                handleShow();
              }}
            >
              <FontAwesomeIcon icon={faCalculator} />
              Calculate
            </button>
            <button
              className="btn btn-success"
              onClick={() => {
                if (!status) {
                  setPrintContainer(false);
                } else {
                  setPrintContainer(true);
                  SalesMaster();
                }
              }}
            >
              <FontAwesomeIcon icon={faPrint} />
              Print
            </button>
            <button className="btn btn-danger">
              <FontAwesomeIcon icon={faCancel} />
              VOID
            </button>
            <button
              className="btn btn-warning"
              onClick={() => setReprint(true)}
            >
              <FontAwesomeIcon icon={faPrint} />
              RePrint
            </button>
            <button className="btn btn-secondary" onClick={() => setOrder([])}>
              <FontAwesomeIcon icon={faRemove} />
              Clear
            </button>
          </div>
          <div className="p-3">
            <div className="pb-3 bg-white">
              {productState && (
                <>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>Qty</th>
                        <th>Item</th>
                        <th>Total Price</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {order.map((item) => {
                        let { _id, productName } = item.data;
                        let quantity = item.quantity;
                        let totalPrice = item.totalPrice;
                        return (
                          <>
                            <tr key={_id}>
                              <td>{quantity}</td>
                              <td>{productName}</td>
                              <td>${totalPrice * zwl}</td>
                              <td>
                                <span
                                  onClick={(e) => {
                                    HandleProductLoad(_id);
                                  }}
                                  style={{ cursor: "pointer" }}
                                >
                                  <FontAwesomeIcon icon={faTrashAlt} />
                                  Delete
                                </span>
                              </td>
                            </tr>
                          </>
                        );
                      })}
                    </tbody>
                  </table>
                </>
              )}
            </div>
          </div>
        </div>
        <div
          className="bg-white col-4 overflow-auto p-3 shadow"
          style={{ height: "95%" }}
        >
          <div>
            <input
              type="search"
              className="form-control"
              id="searchBar"
              placeholder="Start searching products here....."
              onKeyUp={async (e) => {
                let name = e.target.value;
                if (name === "") {
                  setLoading(false);
                } else {
                  await WebAPIServices.GetProductInInvetoryByName(name).then(
                    (jsonResponse) => {
                      console.log(jsonResponse);
                      if (jsonResponse === undefined) {
                        setLoading(false);
                      } else {
                        if (jsonResponse.returnStatusCode === 200) {
                          setData(jsonResponse.dynamicDomainModel);
                          setLoading(true);
                        } else {
                          setLoading(false);
                        }
                      }
                    }
                  );
                }
              }}
            />
          </div>
          <div>
            <div>
              {loading && (
                <>
                  {data.map((value, key) => {
                    let { _id, productName, imagePath, price } = value;
                    return (
                      <>
                        <div
                          data-product={JSON.stringify(value)}
                          ref={ref}
                          key={key}
                          className="d-flex justify-content-between p-3 border-bottom border-lightgray border-1 z-index-2"
                          onClick={(e) => {
                            let data =
                              e.currentTarget.getAttribute("data-product");
                            const ProductExist = order.find(
                              (item) => item._id === _id
                            );
                            if (ProductExist) {
                              setProductPrice(
                                productPrice + ProductExist.price * zwl
                              );
                              setOrder(
                                order.map((item) =>
                                  item._id === _id
                                    ? {
                                        ...ProductExist,
                                        _id: _id,
                                        data: JSON.parse(data),
                                        quantity: ProductExist.quantity + 1,
                                        price: price,
                                        totalPrice:
                                          price + ProductExist.totalPrice,
                                      }
                                    : item
                                )
                              );
                            } else {
                              setProductPrice(productPrice + price * zwl);
                              setOrder([
                                ...order,
                                {
                                  _id: _id,
                                  data: JSON.parse(data),
                                  quantity: 1,
                                  price: price,
                                  totalPrice: price, // <-- error is here
                                },
                              ]);
                            }
                            setProductState(true);
                            setLoading(false);
                            document.getElementById("searchBar").value = "";
                          }}
                        >
                          <div key={_id}>
                            <img
                              src={imagePath}
                              alt="product_image"
                              width="60px"
                              height="60px"
                            />
                          </div>
                          <div
                            className="d-flex justify-content-around"
                            style={{ width: "100%" }}
                          >
                            <span>{productName}</span>
                            <span>{"   $" + price * zwl}</span>
                          </div>
                        </div>
                      </>
                    );
                  })}
                </>
              )}
            </div>

            {printContainer && (
              <>
                <div className="mt-3 p-3">
                  <h3 className="text-center mb-3">RECEIPT</h3>
                  <p>Branch........4th street harare</p>
                  <p>Store number 40489332</p>
                  <p>Manager Taurai</p>
                  <p>12:00:00:00:00:00</p>
                  <h6>Order...#1350</h6>
                </div>
                <div>
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <th>Menu</th>
                        <th>Price</th>
                        <th>Qty</th>
                        <th>Total Price</th>
                      </tr>
                    </thead>
                    <tbody>
                      {order.map((item) => {
                        let { productName, price } = item.data;
                        let quantity = item.quantity;
                        let totalPrice = item.totalPrice;
                        return (
                          <>
                            <tr>
                              <td>{productName}</td>
                              <td>${price}</td>
                              <td>{quantity}</td>
                              <td>${totalPrice}</td>
                            </tr>
                          </>
                        );
                      })}
                      <tr>
                        <th>Total amount</th>
                        <td></td>
                        <td></td>
                        <td>${productPrice}</td>
                      </tr>
                    </tbody>
                  </table>
                  <h6 className="text-center">
                    <i>Thanks for buying with INSOLAR, come again!!!!!!!</i>
                  </h6>
                </div>
              </>
            )}
            <div></div>
          </div>
          <div className="d-flex justify-content-between align-items-center border-bottom border-2 mt-3 d-none">
            <div>
              <img
                src="https://5.imimg.com/data5/PW/PI/MY-4530978/solar-lamp-500x500.jpg"
                alt=""
                width="40px"
                height="40px"
              />
            </div>
            <div className="d-flex">
              <h6>Solar brands</h6>
            </div>
          </div>
        </div>
      </div>
      <Modal
        show={show}
        onHide={handleClose}
        onEntered={() => document.querySelector(".box").focus()}
      >
        <Modal.Header closeButton>
          <Modal.Title>
            <span>Calculator</span>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="p-4 bg-white border border-3 border-light rounded text-dark">
            <div className="d-flex justify-content-between align-items-center">
              <div>
                <input
                  type="text"
                  className="border-0 border-white shadow-none form-control fs-2 box"
                  placeholder="Enter charge here....."
                />
              </div>
              <div className="z-index-1">
                <h1
                  className="text-secondary z-index-1"
                  onClick={() => BackSpace()}
                >
                  <FontAwesomeIcon icon={faBackspace} />
                </h1>
              </div>
            </div>
          </div>
          <div className="p-3">
            <div className="d-flex justify-content-between align-items-center">
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">1</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">2</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">3</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">+</h1>
              </div>
            </div>
            <div className="d-flex justify-content-between align-items-center mt-2">
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center"> 4</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">5</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center"> 6</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">-</h1>
              </div>
            </div>
            <div className="d-flex justify-content-between align-items-center mt-2">
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">7</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center"> 8</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center"> 9</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">/</h1>
              </div>
            </div>
            <div className="d-flex justify-content-between align-items-center mt-2">
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">*</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center"> 0</h1>
              </div>
              <div
                className="shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={(e) => UserEvent(e)}
              >
                <h1 className="text-center">.</h1>
              </div>
              <div
                className="d-flex align-items-center shadow rounded p-2"
                style={{ width: "70px", height: "70px", cursor: "pointer" }}
                onClick={() => {
                  if (rate) {
                    let rate = document.querySelector(".box").value;
                    setProductPrice(productPrice * rate);
                    zwlRate(rate);
                    setRate(false);
                    handleClose();
                  } else {
                    HandlePayment();
                  }
                }}
              >
                <img src="/images/enter.png" alt="enter_key" />
              </div>
            </div>
          </div>
        </Modal.Body>
      </Modal>
      <Modal show={todo}>
        <Modal.Header closeButton onClick={() => setTodo(false)}></Modal.Header>
        <Modal.Body>
          <div>
            <h1>{user.firstName}</h1>
            <p className="fs-4">{user.lastName}</p>
          </div>
          <div className="text-center fs-3">
            <hr />
            <NavItem
              style={{ cursor: "pointer" }}
              onClick={() => {
                setTodo(false);
                setFModal(true);
              }}
            >
              Finances
            </NavItem>
            <hr />
            <NavItem
              style={{ cursor: "pointer" }}
              onClick={() => {
                setTodo(false);
                setOldPassword(user.password);
                setPModal(true);
              }}
            >
              PasswordChange
            </NavItem>
            <hr />
            <NavItem
              style={{ cursor: "pointer" }}
              onClick={() => {
                setTodo(false);
                setDModal(true);
              }}
            >
              Day End
            </NavItem>
            <hr />
            <NavItem
              style={{ cursor: "pointer" }}
              onClick={() => {
                navigate("/Login");
                localStorage.removeItem("user");
              }}
            >
              Logout
            </NavItem>
          </div>
        </Modal.Body>
      </Modal>

      <Modal show={reprint}>
        <Modal.Header closeButton onClick={() => setReprint(false)}>
          <h3>Order Re-Print</h3>
        </Modal.Header>
        <input
          type="search"
          className="form-control"
          placeholder="Place order number here"
          onKeyUp={async (e) => {
            await WebAPIServices.OrderReprint(e.target.value)
              .then((jsonResponse) => {
                console.log(jsonResponse);
                if (jsonResponse.returnStatusCode === 200) {
                  setOrderNumber(jsonResponse.dynamicDomainModel.orderNumber);
                  setOrderDetails(jsonResponse.dynamicDomainModel.orderDetails);
                  setRate(jsonResponse.dynamicDomainModel.rate);
                } else {
                  setOrderDetails([]);
                }
              })
              .catch((err) => err);
          }}
        />
        <Modal.Body>
          {orderDetails.length > 0 ? (
            <>
              <h4 className="text-center">
                Order Number: <span className="text-danger">{orderNumber}</span>
              </h4>
              <div className="p-3">
                <table className="table">
                  <thead>
                    <tr>
                      <th>Qty</th>
                      <th>Product Name</th>
                      <th>Price</th>
                    </tr>
                  </thead>
                  <tbody>
                    {orderDetails.map((item) => {
                      return (
                        <>
                          <tr>
                            <td>{item.quantity}</td>
                            <td>{item.productName}</td>
                            <td>
                              {rate > 1 ? (
                                <>ZWL{item.price * rate}</>
                              ) : (
                                <>${item.price}</>
                              )}
                            </td>
                          </tr>
                        </>
                      );
                    })}
                  </tbody>
                </table>
              </div>
              <div className="d-grid fs-4">
                <button
                  className="btn btn-block btn-warning"
                  onClick={() => {
                    setRate(1);
                    setOrderDetails([]);
                    setReprint(false);
                  }}
                >
                  Complete reprint
                </button>
              </div>
            </>
          ) : (
            <div>
              <h4 className="text-danger text-center p-4">
                <i>Please Place an order number</i>
              </h4>
              <div className="d-grid fs-4">
                <button
                  className="btn btn-block btn-secondary"
                  onClick={() => {
                    setOrderDetails([]);
                    setReprint(false);
                  }}
                >
                  Dismiss
                </button>
              </div>
            </div>
          )}
        </Modal.Body>
      </Modal>
      <Modal
        show={fModal}
        onShow={async () => {
          await WebAPIServices.MyFinances(
            new Date().toLocaleDateString(),
            cashierID
          )
            .then((jsonResponse) => {
              console.log(jsonResponse);
              if (jsonResponse.returnStatusCode === 200) {
                setFinances(jsonResponse.dynamicDomainModel.sales);
                setSum(jsonResponse.dynamicDomainModel.sum);
              }
            })
            .catch((err) => err);
        }}
      >
        <Modal.Header closeButton onClick={() => setFModal(false)}>
          <h3>My Finances</h3>
        </Modal.Header>
        <Modal.Body>
          {finances.length > 0 && (
            <>
              {finances.map((data) => {
                return (
                  <>
                    {data.orderDetails.map((item) => {
                      return (
                        <>
                          <div>
                            <div className="d-flex justify-content-between p-2">
                              <span>
                                {item.quantity} {item.productName}{" "}
                                {item.Rate > 1
                                  ? "ZWL " + item.price * item.rate
                                  : "$" + item.price}
                              </span>
                              <span>{data.createdDateString}</span>
                            </div>
                            <hr />
                          </div>
                        </>
                      );
                    })}
                  </>
                );
              })}
            </>
          )}
          <h4 className="text-danger p-3">Total Finances = ${sum}</h4>
          <div className="d-grid">
            <button
              className="btn btn-warning"
              onClick={() => {
                setFinances([]);
                setFModal(false);
              }}
            >
              Close Finances
            </button>
          </div>
        </Modal.Body>
      </Modal>
      <Modal
        show={dModal}
        onShow={async () => {
          await WebAPIServices.DayEnd(cashierID)
            .then((jsonResponse) => {
              console.log(jsonResponse);
              if (jsonResponse.returnStatusCode === 200) {
                setDayEnd(jsonResponse.dynamicDomainModel);
              }
            })
            .catch((err) => err);
        }}
      >
        <Modal.Header closeButton onClick={() => setDModal(false)}>
          <h3>Day End</h3>
        </Modal.Header>
        <Modal.Body>
          {dayEnd.length > 0 && (
            <>
              {dayEnd.map((data) => {
                return (
                  <>
                    {data.orderDetails.map((item) => {
                      return (
                        <>
                          <div>
                            <div className="d-flex justify-content-between p-2">
                              <span>
                                {item.quantity} {item.productName}{" "}
                                {item.Rate > 1
                                  ? "ZWL " + item.price * item.rate
                                  : "$" + item.price}
                              </span>
                              <span>{data.createdDateString}</span>
                            </div>
                            <hr />
                          </div>
                        </>
                      );
                    })}
                    <div className="d-grid">
                      <button
                        className="btn btn-warning"
                        onClick={() => {
                          setDayEnd([]);
                          setDModal(false);
                        }}
                      >
                        Finish off day end
                      </button>
                    </div>
                  </>
                );
              })}
            </>
          )}
        </Modal.Body>
      </Modal>

      {/* Update Passwords  */}
      <Modal show={pModal}>
        <Modal.Header closeButton onClick={() => setPModal(false)}>
          <h3>Password Change</h3>
        </Modal.Header>
        <Modal.Body>
          <Form.Group className="mb-3" controlId="formBasicUser">
            <Form.Label>Old Password</Form.Label>
            <Form.Control
              type="password"
              defaultValue="00000"
              disabled={true}
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicUser">
            <Form.Label>New Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Enter new password"
              onKeyUp={(e) => setNewPassword(e.target.value)}
            />
          </Form.Group>
          <div className="d-grid">
            <button
              type="button"
              className="btn btn-primary"
              onClick={async () => {
                var profileDomainModel = {
                  _id: user._id,
                  FirstName: "",
                  LastName: "",
                  FullName: "",
                  Salt: "",
                  Password: newPassword,
                  Email: "",
                  Role: 1,
                  DateCreated: "2022-11-28T04:52:42.038Z",
                  DateModified: "2022-11-28T04:52:42.038Z",
                  Status: 1,
                };
                await WebAPIServices.PasswordChange(profileDomainModel)
                  .then((jsonResponse) => {
                    console.log(jsonResponse);
                  })
                  .catch((err) => err);
                setPModal(false);
              }}
            >
              Change Password
            </button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default SellersPortal;
