import { useState, useEffect } from "react";
import HeaderTemplate from "../../page_templates/header_template";
import {
  faBoxesPacking,
  faBoxOpen,
  faMoneyBillAlt,
  faMoneyBills,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SidePane from "../../page_templates/side_pane";
import { Row, Col } from "react-bootstrap";
import React from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend,
} from "chart.js";
import { Line } from "react-chartjs-2";
import WebAPIServices from "../../../serv/apiservices";
const DashBoard = () => {
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend
  );

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: false,
        text: "Sales",
      },
    },
  };

  const [loading, setLoading] = useState(false);
  const [orders, setOrders] = useState([]);
  const [orderCount, setOrderCount] = useState(0);
  const [turnover, setTurnover] = useState(0);
  const [revenue, setRevenue] = useState(0);
  const [number, setNumber] = useState({});
  const [number1, setNumber1] = useState({});
  const [number2, setNumber2] = useState({});
  const [yes, setYes] = useState(0);
  const [sales, setSales] = useState(0);
  const [stock, setStock] = useState(0);
  const [localDate, setLocalDate] = useState(new Date().toLocaleTimeString());

  useEffect(() => {
    Data();
  }, []);

  const Data = async () => {
    await WebAPIServices.SalesReport()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setYes(jsonResponse.dynamicDomainModel.salesYesterday);
        setSales(jsonResponse.dynamicDomainModel.totalSales);
        setStock(jsonResponse.dynamicDomainModel.stock);
        setNumber1(jsonResponse.dynamicDomainModel.today);
        setNumber2(jsonResponse.dynamicDomainModel.yesterday);
        setLoading(true);
      })
      .catch((err) => err);
    await WebAPIServices.TotalSales(localDate)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setLoading(true);
          setNumber(jsonResponse.dynamicDomainModel.totalSales);
          let arr = jsonResponse.dynamicDomainModel.orderDetails;
          setTurnover(jsonResponse.dynamicDomainModel.turnover);
          setOrderCount(jsonResponse.dynamicDomainModel.allOrders);
          setRevenue(jsonResponse.dynamicDomainModel.allRevenue);
          setOrders(arr);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };

  const data = {
    datasets: [
      {
        fill: false,
        label: "Today",
        data: number1,
        borderColor: "rgb(53, 162, 235)",
        backgroundColor: "rgb(53, 162, 235)",
      },
      {
        fill: false,
        label: "Yesterday",
        data: number2,
        borderColor: "rgb(255, 165, 0)",
        backgroundColor: "rgb(255, 165, 0)",
      },
    ],
  };

  const data2 = {
    datasets: [
      {
        fill: true,
        label: "Sales",
        data: number,
        lineTension: 0.4,
        borderColor: "rgb(53, 162, 235)",
        backgroundColor: "rgba(53, 162, 235, 0.5)",
      },
    ],
  };

  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          {loading && (
            <>
              <Row
                lg={3}
                md={2}
                sm={1}
                xs={1}
                className="col-12 justify-content-between align-items-center p-3"
              >
                <Col className="mt-3">
                  <div
                    lg={7}
                    md={6}
                    sm={12}
                    xs={12}
                    className="card rounded-0 bg-secondary text-white"
                  >
                    <div className="card-body" style={{ height: "90px" }}>
                      <h5>
                        <FontAwesomeIcon icon={faMoneyBillAlt} />
                        Total Sales
                      </h5>
                      <h5>{sales}</h5>
                    </div>
                    <div
                      className="card-footer"
                      style={{ height: "30px" }}
                    ></div>
                  </div>
                </Col>
                <Col className="mt-3">
                  <div
                    lg={7}
                    md={6}
                    sm={12}
                    xs={12}
                    className="card rounded-0 bg-light text-dark"
                  >
                    <div
                      className="card-body text-dark"
                      style={{ height: "90px" }}
                    >
                      <h5>
                        <FontAwesomeIcon icon={faMoneyBills} />
                        Sales Yesterday
                      </h5>
                      <h5>{yes}</h5>
                    </div>
                    <div
                      className="card-footer"
                      style={{ height: "30px" }}
                    ></div>
                  </div>
                </Col>
                <Col className="mt-3">
                  <div
                    lg={7}
                    md={6}
                    sm={12}
                    xs={12}
                    className="card rounded-0 bg-danger text-dark"
                  >
                    <div
                      className="card-body text-white"
                      style={{ height: "90px" }}
                    >
                      <h5>
                        <FontAwesomeIcon icon={faBoxOpen} />
                        Remaining Stock
                      </h5>
                      <h5>{stock}</h5>
                    </div>
                    <div
                      className="card-footer"
                      style={{ height: "30px" }}
                    ></div>
                  </div>
                </Col>
              </Row>
              <div className="mt-3 p-2 overflow-auto">
                <span>Recent activities</span>
                <Line options={options} data={data} />
                <div className="d-flex flex-wrap">
                  <div className="col-8">
                    <span className="d-flex">
                      <h5 className="p-2">Filter :</h5>
                      <input
                        type="month"
                        className="form-control"
                        style={{ width: "250px" }}
                        onChange={(e) => {
                          setLocalDate(e.target.value);
                        }}
                      />
                    </span>
                    <Line options={options} data={data2} />
                  </div>
                  <div className="col-4">
                    <h5>Top selling</h5>
                    <table className="table w-100 col-12">
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>ProductName</th>
                          <th>Profit</th>
                          <th>Sales</th>
                        </tr>
                      </thead>
                      <tbody>
                        {orders.map((item, key) => {
                          let total;
                          if (revenue === 0) {
                            total = (item.value / 1) * 100;
                          } else {
                            total = (item.value / revenue) * 100;
                          }

                          let text;
                          if (total >= 50) {
                            text = "text-success";
                          } else if (total >= 40 && total <= 49) {
                            text = "text-warning";
                          } else {
                            text = "text-danger";
                          }
                          return (
                            <>
                              <tr key={key}>
                                <td>{key + 1}</td>
                                <td>{item.key}</td>
                                <td className={text}>
                                  {total.toString().includes("Infi")
                                    ? 0
                                    : total.toString().substring(0, 4)}{" "}
                                  %
                                </td>
                                <td>{item.value}</td>
                              </tr>
                            </>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default DashBoard;
