import HeaderTemplate from "../../page_templates/header_template";
import SidePane from "../../page_templates/side_pane";
import { useState, useEffect } from "react";
import WebAPIServices from "../../../serv/apiservices";
import { Form } from "react-bootstrap";
const StockTakeReport = () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [categoryId, setCategoryID] = useState("");
  const [categories, setCategories] = useState([]);
  const [positiveMargin, setPositiveMargin] = useState([]);
  const [negativeMargin, setNegativeMargin] = useState([]);

  useEffect(() => {
    GetCategories();
  }, []);

  const Data = async () => {
    await WebAPIServices.StockTakeReport(startDate, endDate, categoryId)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setLoading(true);
          setPositiveMargin(jsonResponse.dynamicDomainModel.positiveMargin);
          setNegativeMargin(jsonResponse.dynamicDomainModel.negativeMargin);
        }
      })
      .catch((err) => err);
  };

  const GetCategories = async () => {
    await WebAPIServices.GetCategories()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setCategories(jsonResponse.dynamicDomainModel);
          setCategoryID(jsonResponse.dynamicDomainModel[0]._id);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <h3>Stock take sheet</h3>
            <div>
              <Form.Group className="mb-3 col-3" controlId="formBasicProduct">
                <Form.Label>Category</Form.Label>
                <Form.Select onClick={(e) => setCategoryID(e.target.value)}>
                  {categories.map((value) => {
                    let { _id, categoryName } = value;
                    return (
                      <>
                        <option value={_id}>{categoryName}</option>
                      </>
                    );
                  })}
                </Form.Select>
              </Form.Group>
            </div>
            <div className="d-flex justify-content-around col-8 pt-2">
              <div>
                <div className="input-group">
                  StartDate:
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setStartDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <div className="input-group">
                  End Date:
                  <input
                    type="date"
                    className="form-control"
                    onChange={(e) => setEndDate(e.target.value)}
                  />
                </div>
              </div>
              <div>
                <button
                  className="btn btn-sm btn-primary"
                  onClick={() => Data()}
                >
                  Generate report
                </button>
              </div>
            </div>
            <div className="d-flex justify-content-around mt-5">
              <div className="border border-light col-5 p-3">
                <h5>Negative stock</h5>
                <div>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Category</th>
                        <th>Margin</th>
                        <th>Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {loading && negativeMargin.length > 0 && (
                        <>
                          {negativeMargin.map((item, key) => {
                            return (
                              <>
                                <tr key={key}>
                                  <th>{key + 1}</th>
                                  <td>{item.categoryName}</td>
                                  <td>{item.margin}</td>
                                  <td>{item.dateModifiedString}</td>
                                </tr>
                              </>
                            );
                          })}
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
              <div className="border border-light col-5 p-3">
                <h5>Positive stock</h5>
                <div>
                  <table className="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Category</th>
                        <th>Margin</th>
                        <th>Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {loading && positiveMargin.length > 0 && (
                        <>
                          {positiveMargin.map((item, key) => {
                            return (
                              <>
                                <tr key={key}>
                                  <th>{key + 1}</th>
                                  <td>{item.categoryName}</td>
                                  <td>{item.margin}</td>
                                  <td>{item.dateModified}</td>
                                </tr>
                              </>
                            );
                          })}
                        </>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default StockTakeReport;
