import SidePane from "../../page_templates/side_pane";
import HeaderTemplate from "../../page_templates/header_template";
import { Modal, Form, Nav } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import {
  faCheck,
  faDownload,
  faFile,
  faPrint,
  faRemove,
  faThumbsUp,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { useState, useEffect } from "react";
import WebAPIServices from "../../../serv/apiservices";

const PurchaseOrder = () => {
  const [refresh, setRefresh] = useState(false);
  const [id, setIDs] = useState([]);
  const [data, setData] = useState([]);
  const [state, setState] = useState(false);
  const [preview, setPreview] = useState(false);
  const [documentPreview, setDocumentPreview] = useState([]);
  const [_id, setID] = useState("");
  const [createdby, setCreatedBy] = useState("");
  const [acocuntID, setAccountID] = useState("");
  let user = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    Data();
  }, [refresh]);
  const Data = async () => {
    await WebAPIServices.GetAllProducts()
      .then((jsonResponse) => {
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
          setState(true);
        } else {
          setState(false);
        }
      })
      .catch((err) => err);

    await WebAPIServices.GetCategories()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setCategories(jsonResponse.dynamicDomainModel);
          setCategoryID(jsonResponse.dynamicDomainModel[0]._id);
        } else {
          setLoading(false);
        }
      })
      .catch((err) => err);
  };
  const AddToState = (_id) => {
    if (id.includes(_id)) {
      let filter = id.filter((e) => e !== _id);
      setIDs(filter);
    } else {
      id.push(_id);
    }
    console.log(id);
  };

  const [loading, setLoading] = useState(false);
  const [productName, setProductName] = useState("");
  const [productPrice, setProductPrice] = useState("");
  const [productVariation, setProductVariation] = useState("");
  const [productDescription, setProductDescription] = useState("");
  const [serialNumber, setSerialNumber] = useState("");
  const [statuses, setStatuses] = useState([]);
  const [stateType, setStateType] = useState(0);
  const [productImage, setProductImage] = useState("");
  const [categoryId, setCategoryID] = useState("");
  const [categories, setCategories] = useState([]);
  const [prod, setProd] = useState(false);
  const [supp, setSupp] = useState(false);
  const [rep, setRep] = useState(false);
  const [supplierName, setSupplierName] = useState("");
  const [supplierAddress, setSupplierAddress] = useState("");
  const [vat, setVat] = useState("");
  const [accountNumber, setAccountNumber] = useState("");
  const [companyPhone1, setCompanyPhone1] = useState("");
  const [companyPhone2, setCompanyPhone2] = useState("");
  const [repName, setRepName] = useState("");
  const [repPhone, setRepPhone] = useState("");
  const [supCheck, setSupCheck] = useState(false);
  const [prodCheck, setProdCheck] = useState(false);
  const [repCheck, setRepCheck] = useState(false);
  const [email, setEmail] = useState("");
  const [purchasesId, setPurchasesId] = useState("");
  const [creditorsId, setCreditorsId] = useState("");
  const [repAddress, setRepAddress] = useState("");

  const CreateProduct = async () => {
    var supplierDomainModel = {
      _id: "",
      SupplierName: supplierName,
      Address: supplierAddress,
      VatNumber: vat,
      Email: email,
      AccountNumber: accountNumber,
      CompanyPhone1: companyPhone1,
      CompanyPhone2: companyPhone2,
    };
    var productDomainModel = {
      _id: "aaa080056af5483fb47b8f1be70bfcf7",
      categoryName: "",
      categoryID: categoryId,
      productID: 1000,
      productName: productName,
      suppliersName: supplierName,
      productDescription: productDescription,
      productSpecifications: "",
      price: productPrice,
      weight: "",
      imagePath: productImage,
      serialNumber: serialNumber,
      variation: productVariation,
      status: 1,
      productStatus: 0,
      createdDateString: new Date().toLocaleString(),
      dateModifiedString: "",
      createdBy: user.fullName,
      modifiedBy: "",
      productTimes: {
        dateCreated: "2022-11-28T04:52:42.038Z",
        dateModified: "2022-11-28T04:52:42.038Z",
        obsoluteDate: "2022-11-28T04:52:42.038Z",
        shipmentStartDate: "2022-11-28T04:52:42.038Z",
        shipmentProcesingDate: "2022-11-28T04:52:42.038Z",
        shipmentEndDate: "2022-11-28T04:52:42.038Z",
      },
    };
    var salesPerson = {
      _id: "",
      Fullname: repName,
      Phone: repPhone,
      Address: repAddress,
    };

    var purchaseDomainModel = {
      _id: "",
      SalesPerson: salesPerson,
      Supplier: supplierDomainModel,
      ProductDomainModel: productDomainModel,
      PurchaseDateString: new Date().toLocaleDateString(),
      PurchaseDate: "2022-11-28T04:52:42.038Z",
    };

    await WebAPIServices.CreateProduct(purchaseDomainModel).then(
      async (jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
        setSupCheck(false);
        setProdCheck(false);
        setRepCheck(false);
        await WebAPIServices.GetDoubleEntryID("Purchases")
          .then(async (jsonResponse) => {
            console.log(jsonResponse);
            var doubleEntryDomainModel = {
              _id: "",
              AccountID: jsonResponse.dynamicDomainModel.accountID,
              AccountsAffected: [
                {
                  _id: "",
                  AccountID: jsonResponse.dynamicDomainModel.accountID,
                  AccountType: 1,
                  AccountAffected: productName,
                  Account: "credit",
                  Price: productPrice,
                  DateCreated: "2022-11-28T04:52:42.038Z",
                  DateCreatedString: new Date().toLocaleDateString(),
                },
              ],
              DateCreated: "2022-11-28T04:52:42.038Z",
            };
            await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
              .then(async (jsonResponse) => {
                console.log(jsonResponse);
                await WebAPIServices.GetDoubleEntryID("Sales")
                  .then(async (jsonResponse) => {
                    console.log(jsonResponse);
                    var doubleEntryDomainModel = {
                      _id: "",
                      AccountID: jsonResponse.dynamicDomainModel.accountID,
                      AccountsAffected: [
                        {
                          _id: "",
                          AccountID: jsonResponse.dynamicDomainModel.accountID,
                          AccountType: 0,
                          AccountAffected: supplierName,
                          Account: "credit",
                          Price: productPrice,
                          DateCreated: "2022-11-28T04:52:42.038Z",
                          DateCreatedString: new Date().toLocaleDateString(),
                        },
                      ],
                      DateCreated: "2022-11-28T04:52:42.038Z",
                    };
                    await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
                      .then(() => {
                        console.log(jsonResponse);
                      })
                      .catch((err) => err);
                  })
                  .catch((err) => err);
              })
              .catch((err) => err);
          })
          .catch((err) => err);
      }
    );
  };

  const ApproveProduct = async (productID) => {
    await WebAPIServices.ApproveProduct(productID)
      .then(async (jsonResponse) => {
        console.log(jsonResponse);
        await WebAPIServices.GetDoubleEntryID("Purchases").then(
          async (jsonResponse) => {
            console.log(jsonResponse);
            var doubleEntryDomainModel = {
              _id: "",
              AccountID: jsonResponse.dynamicDomainModel.accountID,
              AccountsAffected: [
                {
                  _id: "",
                  AccountID: jsonResponse.dynamicDomainModel.accountID,
                  AccountType: 1,
                  AccountAffected: productName,
                  Account: "credit",
                  Price: productPrice,
                  DateCreated: "2022-11-28T04:52:42.038Z",
                  DateCreatedString: new Date().toLocaleDateString(),
                },
              ],
              DateCreated: "2022-11-28T04:52:42.038Z",
            };
            await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
              .then(async (jsonResponse) => {
                console.log(jsonResponse);
                await WebAPIServices.GetDoubleEntryID("Sales")
                  .then(async (jsonResponse) => {
                    console.log(jsonResponse);
                    var doubleEntryDomainModel = {
                      _id: "",
                      AccountID: jsonResponse.dynamicDomainModel.accountID,
                      AccountsAffected: [
                        {
                          _id: "",
                          AccountID: jsonResponse.dynamicDomainModel.accountID,
                          AccountType: 0,
                          AccountAffected: supplierName,
                          Account: "credit",
                          Price: productPrice,
                          DateCreated: "2022-11-28T04:52:42.038Z",
                          DateCreatedString: new Date().toLocaleDateString(),
                        },
                      ],
                      DateCreated: "2022-11-28T04:52:42.038Z",
                    };
                    await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
                      .then(() => {
                        console.log(jsonResponse);
                      })
                      .catch((err) => err);
                  })
                  .catch((err) => err);
              })
              .catch((err) => err);
          }
        );
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const HandleImage = async (event) => {
    var image = event.target.files[0];
    const formData = new FormData();
    formData.append("image", image);

    await WebAPIServices.UploadImage(formData)
      .then((jsonResponse) => {
        let { imageURL } = jsonResponse.dynamicDomainModel;
        setProductImage(imageURL);
        console.log(imageURL);
      })
      .catch((err) => err);
  };

  const ApproveProducts = async () => {
    await WebAPIServices.ApproveProducts(id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
        let asyncOpp = async () => {
          let prodName;
          let suppliersName;
          let prodPrice;
          await WebAPIServices.GetProductAndSupplier(id).then(
            (jsonResponse) => {
              console.log(jsonResponse);
              prodName = jsonResponse.dynamicDomainModel.productName;
              suppliersName = jsonResponse.dynamicDomainModel.supplierName;
              prodPrice = jsonResponse.dynamicDomainModel.productPrice;
            }
          );
          await WebAPIServices.GetDoubleEntryID("Purchases").then(
            async (jsonResponse) => {
              console.log(jsonResponse);
              var doubleEntryDomainModel = {
                _id: "",
                AccountID: jsonResponse.dynamicDomainModel.accountID,
                AccountsAffected: [
                  {
                    _id: "",
                    AccountID: jsonResponse.dynamicDomainModel.accountID,
                    AccountType: 1,
                    AccountAffected: prodName,
                    Account: "debit",
                    Price: prodPrice,
                    DateCreated: "2022-11-28T04:52:42.038Z",
                    DateCreatedString: new Date().toLocaleDateString(),
                  },
                ],
                DateCreated: "2022-11-28T04:52:42.038Z",
              };
              await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
                .then(async (jsonResponse) => {
                  console.log(jsonResponse);
                  await WebAPIServices.GetDoubleEntryID("Sales")
                    .then(async (jsonResponse) => {
                      console.log(jsonResponse);
                      var doubleEntryDomainModel = {
                        _id: "",
                        AccountID: jsonResponse.dynamicDomainModel.accountID,
                        AccountsAffected: [
                          {
                            _id: "",
                            AccountID:
                              jsonResponse.dynamicDomainModel.accountID,
                            AccountType: 0,
                            AccountAffected: suppliersName,
                            Account: "debit",
                            Price: prodPrice,
                            DateCreated: "2022-11-28T04:52:42.038Z",
                            DateCreatedString: new Date().toLocaleDateString(),
                          },
                        ],
                        DateCreated: "2022-11-28T04:52:42.038Z",
                      };
                      await WebAPIServices.DoubleEntry(doubleEntryDomainModel)
                        .then(() => {
                          console.log(jsonResponse);
                        })
                        .catch((err) => err);
                    })
                    .catch((err) => err);
                })
                .catch((err) => err);
            }
          );
        };
        for (var i = 0; i <= id.length; i++) {
          asyncOpp();
        }
      })
      .catch((err) => err);
  };

  const GetProduct = async (_id) => {
    await WebAPIServices.GetProduct(_id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setDocumentPreview(jsonResponse.dynamicDomainModel);
          setID(_id);
          setPreview(true);
        }
      })
      .catch((err) => err);
  };

  const GeneratePDF = async (ProductID) => {
    await WebAPIServices.GeneratePdf(ProductID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          window.location.href = jsonResponse.dynamicDomainModel.documentUrl;
        }
      })
      .catch((err) => err);
  };

  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="d-flex justify-content-end align-items-right mb-3">
            <div className="col-12 col-md-8 col-lg-5">
              <input
                type="search"
                placeholder="Start searching any product....."
                className="form-control mr-3 mt-3"
                onChange={async (e) => {
                  let name = e.target.value;
                  if (name === "") {
                    Data();
                  } else {
                    await WebAPIServices.GetProductByName(name)
                      .then((jsonResponse) => {
                        console.log(jsonResponse);
                        if (jsonResponse.returnStatusCode === 200) {
                          setState(true);
                          setData(jsonResponse.dynamicDomainModel);
                        } else {
                          setState(false);
                        }
                      })
                      .catch((err) => err);
                  }
                }}
              />
            </div>
          </div>
          <div className="d-flex p-3">
            <button
              className="btn btn-sm btn-light"
              onClick={() => setSupp(!supp)}
            >
              {supCheck ? (
                <>
                  <FontAwesomeIcon icon={faCheck} className="text-success" />
                </>
              ) : (
                <>
                  <FontAwesomeIcon icon={faRemove} className="text-danger" />
                </>
              )}
              Supplier Infomation
            </button>
            <button
              className="btn btn-sm btn-light"
              onClick={() => setProd(!prod)}
            >
              {prodCheck ? (
                <>
                  <FontAwesomeIcon icon={faCheck} className="text-success" />
                </>
              ) : (
                <>
                  <FontAwesomeIcon icon={faRemove} className="text-danger" />
                </>
              )}
              Purchase product
            </button>
            <button
              className="btn btn-sm btn-light"
              onClick={() => setRep(!rep)}
            >
              {repCheck ? (
                <>
                  <FontAwesomeIcon icon={faCheck} className="text-success" />
                </>
              ) : (
                <>
                  <FontAwesomeIcon icon={faRemove} className="text-danger" />
                </>
              )}
              Sales person
            </button>
            <button
              className="btn btn-sm btn-primary"
              onClick={() => CreateProduct()}
            >
              Submit data
            </button>
          </div>

          <Modal show={prod} onHide={() => setProd(!prod)}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Purchase Product(s)</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Category</Form.Label>
                  <Form.Select onClick={(e) => setCategoryID(e.target.value)}>
                    {categories.map((value) => {
                      let { _id, categoryName } = value;
                      return (
                        <>
                          <option value={_id}>{categoryName}</option>
                        </>
                      );
                    })}
                  </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicProduct">
                  <Form.Label>Product Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product name"
                    onKeyUp={(e) => setProductName(e.target.value)}
                    name="productName"
                    required="true"
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter product price"
                    onKeyUp={(e) => setProductPrice(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Serial Number</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Serial Number"
                    onKeyUp={(e) => setSerialNumber(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Variation</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Product Variation"
                    onKeyUp={(e) => setProductVariation(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Product Description</Form.Label>
                  <textarea
                    rows="4"
                    className="form-control"
                    onKeyUp={(e) => setProductDescription(e.target.value)}
                    placeholder="None"
                  ></textarea>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicVariation">
                  <Form.Label>Image</Form.Label>
                  <input
                    type="file"
                    className="form-control"
                    placeholder="Enter product variation"
                    onChange={(e) => {
                      HandleImage(e.target.files);
                    }}
                  />
                </Form.Group>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="warning" onClick={() => setProd(!prod)}>
                  Close
                </Button>
                <Button
                  variant="dark"
                  onClick={() => {
                    setProdCheck(true);
                    setProd(!prod);
                  }}
                >
                  Purchase on credit
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
          {/* Creating for supplier */}
          <Modal show={supp} onHide={() => setSupp(!supp)}>
            <Modal.Header closeButton>
              <h3>Supplier Info</h3>
            </Modal.Header>
            <Modal.Body>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Supplier namee</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Supplier name"
                  onKeyUp={(e) => setSupplierName(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>VAT Number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="VAT Number"
                  onKeyUp={(e) => setVat(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Account Number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Account Number"
                  onKeyUp={(e) => setAccountNumber(e.target.value)}
                />
                <Form.Group className="mb-3" controlId="formBasicPrice">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter email"
                    onKeyUp={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Company phone 1</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Company phone 1"
                  onKeyUp={(e) => setCompanyPhone1(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Company phone 2</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Company phone 2"
                  onKeyUp={(e) => setCompanyPhone2(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Address</Form.Label>
                <textarea
                  rows="4"
                  className="form-control"
                  onKeyUp={(e) => setSupplierAddress(e.target.value)}
                  placeholder="None"
                ></textarea>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="warning" onClick={() => setSupp(!supp)}>
                Close
              </Button>
              <Button
                variant="dark"
                onClick={() => {
                  setSupp(!supp);
                  setSupCheck(true);
                }}
              >
                Submit
              </Button>
            </Modal.Footer>
          </Modal>
          <Modal show={rep} onHide={() => setRep(!rep)}>
            <Modal.Header closeButton>
              <h3>Representative Info</h3>
            </Modal.Header>
            <Modal.Body>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Rep namee</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Supplier name"
                  onKeyUp={(e) => setRepName(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Phone number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Representative's phone"
                  onKeyUp={(e) => setRepPhone(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicPrice">
                <Form.Label>Address</Form.Label>
                <textarea
                  rows="4"
                  className="form-control"
                  onKeyUp={(e) => setRepAddress(e.target.value)}
                  placeholder="None"
                ></textarea>
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="warning" onClick={() => setRep(!rep)}>
                Close
              </Button>
              <Button
                variant="dark"
                onClick={() => {
                  setRepCheck(true);
                  setRep(!rep);
                }}
              >
                Submit
              </Button>
            </Modal.Footer>
          </Modal>

          <div className="p-3">
            <button
              className="btn btn-small btn-success mb-3"
              onClick={() => ApproveProducts()}
            >
              <FontAwesomeIcon icon={faThumbsUp} />
              Approve
            </button>
            <table className="table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>ProductName</th>
                  <th>Status</th>
                  <th>Aprove</th>
                  <th>Preview</th>
                  <th>Download</th>
                </tr>
              </thead>
              <tbody>
                {state && (
                  <>
                    {data.map((value) => {
                      let { _id, productName, status } = value;
                      return (
                        <>
                          <tr key={_id}>
                            <td>
                              <input
                                type="checkbox"
                                value={_id}
                                onChange={() => AddToState(_id)}
                              />
                            </td>
                            <td>{productName}</td>
                            <td>{status}</td>
                            <td>
                              <Nav.Link
                                href="#"
                                data-id={_id}
                                onClick={(e) => ApproveProduct(_id)}
                              >
                                <FontAwesomeIcon icon={faThumbsUp} />
                                Approve
                              </Nav.Link>
                            </td>
                            <td>
                              <Nav.Link
                                href="#"
                                data-id={_id}
                                onClick={() => {
                                  GetProduct(_id);
                                }}
                              >
                                <FontAwesomeIcon icon={faFile} />
                                Preview
                              </Nav.Link>
                            </td>
                            <td>
                              <Nav.Link
                                href="#"
                                data-id={_id}
                                onClick={() => GeneratePDF(_id)}
                              >
                                <FontAwesomeIcon icon={faDownload} />
                                Download
                              </Nav.Link>
                            </td>
                          </tr>
                        </>
                      );
                    })}
                  </>
                )}
              </tbody>
            </table>
          </div>
          <Modal show={preview} onHide={() => setPreview(!preview)}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Product preview</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                {documentPreview.map((value) => {
                  let {
                    _id,
                    productName,
                    price,
                    productDescription,
                    serialNumber,
                    createdDateString,
                  } = value;
                  return (
                    <>
                      <div className="p-3" key={_id}>
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold">Product Name</p>
                          <p>{productName}</p>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold">Serial Number</p>
                          <p>{serialNumber}</p>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold">Product Description</p>
                          <p>{productDescription}</p>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold">Created Date</p>
                          <p>{createdDateString}</p>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <h5>Total Price</h5>
                          <h4 className="fw-bold">${price}</h4>
                        </span>
                      </div>
                    </>
                  );
                })}
              </Modal.Body>
              <Modal.Footer>
                <Button variant="primary" onClick={() => setPreview(!preview)}>
                  <FontAwesomeIcon icon={faPrint} />
                  Print
                </Button>
                <Button
                  variant="success"
                  onClick={() => {
                    GeneratePDF(_id);
                    setPreview(!preview);
                  }}
                >
                  <FontAwesomeIcon icon={faDownload} />
                  Download
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
        </div>
      </div>
    </>
  );
};

export default PurchaseOrder;
