import HeaderTemplate from "../../page_templates/header_template";
import SidePane from "../../page_templates/side_pane";
import { useState, useEffect, useRef } from "react";
import { Nav, Form } from "react-bootstrap";
import { Modal } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import {
  faBackspace,
  faCalculator,
  faCancel,
  faComputer,
  faDownload,
  faFile,
  faMoneyBillTrendUp,
  faPrint,
  faRemove,
  faSignOut,
  faSort,
  faTrashAlt,
  faUser,
  faUserGear,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import WebAPIServices from "../../../serv/apiservices";
const Sales = () => {
  const [refresh, setRefresh] = useState(false);
  const [id, setIDs] = useState([]);
  const [data, setData] = useState([]);
  const [state, setState] = useState(false);
  const [preview, setPreview] = useState(false);
  const [documentPreview, setDocumentPreview] = useState([]);
  const [_id, setID] = useState("");
  useEffect(() => {
    Data();
  }, [refresh]);
  const Data = async () => {
    await WebAPIServices.GetSales()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
          setState(true);
        } else {
          setState(false);
        }
      })
      .catch((err) => err);
  };
  const AddToState = (_id) => {
    if (id.includes(_id)) {
      let filter = id.filter((e) => e !== _id);
      setIDs(filter);
    } else {
      id.push(_id);
    }
    console.log(id);
  };

  const ApproveProduct = async (productID) => {
    await WebAPIServices.ApproveProduct(productID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const ApproveProducts = async () => {
    await WebAPIServices.ApproveProducts(id)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        setRefresh(!refresh);
      })
      .catch((err) => err);
  };

  const GetProduct = async (orderNumber) => {
    await WebAPIServices.GetSale(orderNumber)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setDocumentPreview([jsonResponse.dynamicDomainModel]);
          setID(_id);
          setPreview(true);
        }
      })
      .catch((err) => err);
  };

  const GeneratePDF = async (ProductID) => {
    await WebAPIServices.GeneratePdf(ProductID)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          window.location.href = jsonResponse.dynamicDomainModel.documentUrl;
        }
      })
      .catch((err) => err);
  };

  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <div className="d-flex justify-content-end align-items-right mb-3">
              <div className="col-12 col-md-8 col-lg-5">
                <input
                  type="search"
                  placeholder="Start searching any product....."
                  className="form-control mr-3"
                  onChange={async (e) => {
                    let name = e.target.value;
                    if (name === "") {
                      Data();
                    } else {
                      await WebAPIServices.GetSaleById(name)
                        .then((jsonResponse) => {
                          console.log(jsonResponse);
                          if (jsonResponse.returnStatusCode === 200) {
                            setState(true);
                            setData(jsonResponse.dynamicDomainModel);
                          } else {
                            setState(false);
                          }
                        })
                        .catch((err) => err);
                    }
                  }}
                />
              </div>
            </div>
          </div>
          <div className="p-2 overflow-auto">
            <span>Recent activities</span>
            <table className="table table-responsive text-dark">
              <thead>
                <tr className="text-dark">
                  <th scope="col">#</th>
                  <th> Product Details</th>
                  <th scope="col">
                    <FontAwesomeIcon icon={faSort} />
                    Date created / modified
                  </th>
                  <th scope="col">Preview</th>
                  <th scope="col">Download</th>
                </tr>
              </thead>
              <tbody>
                {state && (
                  <>
                    {data.map((value) => {
                      let { _id, orderNumber, orderedDate } = value;
                      return (
                        <>
                          <tr key={_id}>
                            <td>{orderNumber}</td>
                            <td>
                              {value.orderDetails.map((item, index) => {
                                return (
                                  <div>
                                    <span>{"x" + item.quantity}</span>
                                    <span>{"  " + item.productName}</span>
                                  </div>
                                );
                              })}
                            </td>
                            <td>{orderedDate}</td>
                            <td>
                              <Nav.Link
                                href="#"
                                data-id={_id}
                                onClick={() => {
                                  GetProduct(orderNumber);
                                }}
                              >
                                <FontAwesomeIcon icon={faFile} />
                                Preview
                              </Nav.Link>
                            </td>
                            <td>
                              <Nav.Link
                                href="#"
                                data-id={_id}
                                onClick={() => GeneratePDF(_id)}
                              >
                                <FontAwesomeIcon icon={faDownload} />
                                Download
                              </Nav.Link>
                            </td>
                          </tr>
                        </>
                      );
                    })}
                  </>
                )}
              </tbody>
            </table>
          </div>
          <Modal show={preview} onHide={() => setPreview(!preview)}>
            <Form>
              <Modal.Header closeButton>
                <Modal.Title>Product preview</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                {documentPreview.map((value) => {
                  let { _id, orderNumber, paymentType, orderedDate, rate } =
                    value;
                  return (
                    <>
                      <div className="p-3" key={_id}>
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold  text-uppercase">Order No: </p>
                          <p className="fs-2 fw-bold">{orderNumber}</p>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold text-uppercase">
                            order details:
                          </p>
                          <div>
                            {value.orderDetails.map((item) => {
                              return (
                                <>
                                  <div>
                                    <p>
                                      x{item.quantity} {item.productName}{" "}
                                      {paymentType === 0 ? "$" : "ZWL"}
                                      {item.price * rate}
                                    </p>
                                  </div>
                                </>
                              );
                            })}
                          </div>
                        </span>
                        <hr />
                        <span className="d-flex justify-content-between">
                          <p className="fw-bold text-uppercase">
                            Ordered date:{" "}
                          </p>
                          <p>{orderedDate}</p>
                        </span>
                      </div>
                    </>
                  );
                })}
              </Modal.Body>
              <Modal.Footer>
                <Button variant="primary" onClick={() => setPreview(!preview)}>
                  <FontAwesomeIcon icon={faPrint} />
                  Print
                </Button>
                <Button
                  variant="success"
                  onClick={() => {
                    GeneratePDF(_id);
                    setPreview(!preview);
                  }}
                >
                  <FontAwesomeIcon icon={faDownload} />
                  Download
                </Button>
              </Modal.Footer>
            </Form>
          </Modal>
        </div>
      </div>
    </>
  );
};

export default Sales;
