import SidePane from "../../page_templates/side_pane";
import HeaderTemplate from "../../page_templates/header_template";
import { useState, useEffect } from "react";
import { Modal, Form } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import WebAPIServices from "../../../serv/apiservices";

const DoubleEntry = () => {
  const [show, setShow] = useState(false);
  const [accountName, setAccountName] = useState("");
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {
    Data();
  }, [response]);

  const Data = async () => {
    await WebAPIServices.GetAccounts()
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setData(jsonResponse.dynamicDomainModel);
        }
      })
      .catch((err) => err);
  };

  const CreateAccount = async () => {
    var doubleEntryAccountsDomainModel = {
      _id: "",
      AccountName: accountName,
    };
    await WebAPIServices.CreateDBA(doubleEntryAccountsDomainModel)
      .then((jsonResponse) => {
        console.log(jsonResponse);
        if (jsonResponse.returnStatusCode === 200) {
          setLoading(true);
          setResponse(!response);
        }
      })
      .catch((err) => err);
  };
  return (
    <>
      <div
        className="position-absolute d-flex"
        style={{ width: "100%", height: "100%" }}
      >
        <SidePane />
        <div className="col-12 col-lg-10 overflow-auto">
          <HeaderTemplate />
          <div className="p-3">
            <div>
              <button
                className="btn btn-sm btn-primary"
                onClick={() => setShow(true)}
              >
                New Entry
              </button>
            </div>
            <div>
              <table className="table">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>AccountName</th>
                  </tr>
                </thead>
                <tbody>
                  {data.length > 0 && (
                    <>
                      {data.map((item, key) => {
                        return (
                          <>
                            <tr>
                              <th>{key + 1}</th>
                              <td>{item.accountName}</td>
                            </tr>
                          </>
                        );
                      })}
                    </>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      <Modal show={show} onHide={() => setShow(false)}>
        <Form>
          <Modal.Header closeButton>
            <Modal.Title>Product preview</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="modal-body">
              <div className="form-group">
                <label htmlFor="productname" className="mb-3">
                  AccountName
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="productname"
                  placeholder="Enter category name"
                  onKeyUp={(e) => setAccountName(e.target.value)}
                />
              </div>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="success"
              onClick={() => {
                setShow(!show);
                CreateAccount();
              }}
            >
              Create
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
};

export default DoubleEntry;
